<template>
    <div class="confirmOrder">
        <p class="p_head">当前位置--订单管理--确认订单（卖家）</p>
        <div v-if="onff">
            <Table height='615' :columns="order_column" :data="order_data"></Table> 
            <Page :page-size="10" :total="pages" @click="pageChange"></Page>
        </div>
        
        <div v-else>
            <span @click="back" class="back_btn">返回</span>
            <div class="reportGood">
                <label>商品标题</label>
                <span>{{ detailInfo.title }}</span>
            </div>
            <div class="reportGood">
                <label>数量</label>
                <span>{{ detailInfo.goodsNum }}</span>
            </div>
            <div class="reportGood">
                <label>总价</label>
                <span>{{ detailInfo.goodsTotal }}</span>
            </div>
            <div class="reportGood">
                <label>日期</label>
                <span>{{ detailInfo.creatTime }}</span>
            </div>
            <div class="reportGood">
                <label>联系人</label>
                <span>{{ detailInfo.userName }}</span>
            </div>
            <div class="reportGood">
                <label>联系方式</label>
                <span>{{ detailInfo.linkPhone }}</span>
            </div>
            <div class="reportGood">
                <label>留言</label>
                <span>{{ detailInfo.message }}</span>
            </div>
            <div style="margin-left: 80px">
                <Button type="primary" @click="agree">同意交易</Button>
                <Button type="primary" @click="remove">拒绝交易</Button>
            </div>
        </div>

    </div>
</template>
<script>
export default {
    data(){
        return {
            pages: 0,
            order_column: [{
                title: '商品标题',
                key: 'title',
                align: 'center',
                width: 340
            },{  
                title: '买家',
                key: 'userName',
                align: 'center',
                width: 100
            },{
                title: '日期',
                key: 'creatTime',
                align: 'center',
                width: 120
            },{
                title: '操作',
                key: 'action',
                width: 200,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            on: {
                                click: ()=>{
                                    this.onff = false;
                                    this.detailInfo = params.row;
                                    this.detailInfo.index = params.index;
                                }
                            }
                        },'查看详情')
                    ]);
                }
            }],
            order_data: [],
            onff: true
        }
    },
    created: function(){
        this.orderData(1);
    },
    watch: {
        '$route'(to,from){
            if(to.name == 'confirmOrder'){
                this.orderData(1);
                this.onff = true;
            }
        }
    },
    methods: {
        //获取订单数据
        orderData(page){
            let self = this;
            this.$http(process.env.API_HOST+'orders/operateListBySellerId',{
                method: 'get',
                params: {
                    'page': page,
                    'rows': 10 
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.order_data = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;
                    data.forEach(e1 => {
                        let time = e1.creatTime.split(' ');
                        // let d = {
                        //     'title': e1.title,
                        //     'creatTime': time[0],
                        //     'id': e1.id,
                        //     'userName': e1.userName
                        // };
                        let d = e1;
                        d.creatTime = time[0];
                        self.order_data.push(d);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }else{
                    console.log(res);
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //同意交易
        agree(){
            let index = this.detailInfo.index;            
            let self = this;
            let id = this.order_data[index].id;
            this.$http(process.env.API_HOST+'orders/confirm/'+id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 200){
                    self.$Modal.success({
                            title: '提示',
                            content: '<p>已同意交易！</p>',
                            onOk: ()=>{
                                self.back();
                                self.order_data.splice(index,1);
                            }
                        });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //拒绝交易
        remove(){
            let index = this.detailInfo.index;                        
            let id = this.order_data[index].id;
            let self = this;
            this.$http(process.env.API_HOST+'orders/cancel/'+id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 200){
                    self.$Modal.success({
                            title: '提示',
                            content: '<p>已拒绝交易！</p>',
                            onOk: ()=>{
                                self.back();
                                self.order_data.splice(index,1);
                            }
                        });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //页码改变函数
        pageChange(page){
            this.orderData(page);
        },
        //‘返回’函数
        back(){
            this.onff = true;
        }

    }
}
</script>
<style lang="less" scoped>
.confirmOrder{
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .ivu-table-wrapper{
        height: 616px;
    }
    .ivu-page{
        margin-top: 20px;
    }
    .reportGood{
            overflow: hidden;
            margin-bottom: 15px;
        label{
            font-weight: bold;
            display: inline-block;
            width: 80px;
            float: left;
        }
        dl{
            display: inline-block;
            dd{
                float: left;
                margin-right: 15px;
            }
        }
    }
    .back_btn{
        color: #f5866e;
        font-size: 16px;
        cursor: pointer;
        position: relative;
        top: -35px;
        left: 650px;
    }
    .back_btn:hover{
        text-decoration: underline;
    }
}
</style>



