<template>
    <div class="newReport">
        <p class="p_head">当前位置--举报商品</p>
        <div v-if="onff" style="width: 860px">
            <Table height='615' style="margin-right: 100px" :columns="order_columns" :data="orderData"></Table>
            <Page :total="pages" :page-size="10" @on-change="pageChange"></Page>
        </div>

        <div v-else>
            <span @click="back" class="back_btn">返回</span>
            <div class="reportGood">
                <label>商品标题</label>
                <span>{{ detailOrder.title }}</span>
            </div>
            <div class="reportGood">
                <label>购买数量</label>
                <span>{{ detailOrder.goodsNum }}</span>
            </div>
            <div class="reportGood">
                <label>总价</label>
                <span>{{ detailOrder.goodsTotal }}(元)</span>
            </div>
            <div class="reportGood">
                <label>联系方式</label>
                <span>{{ detailOrder.linkPhone }}</span>
            </div>
            <div class="reportGood">
                <label>日期</label>
                <span>{{ detailOrder.creatTime }}</span>
            </div>
            <div class="reportGood">
                <label>举报原因</label>
                <Input type="textarea" style="width: 350px;" v-model.trim="reason"></Input>
            </div>
            <div class="reportGood">
                <label>图片</label>
                <div class="upload">
                    <Upload :before-upload="handleBeforeUpload" :show-upload-list="false" action="#">
                        <div class="camera" style="display: inline-block">
                            <Icon type="camera" size="20" style=""></Icon>
                        </div>
                        <span>最多只能上传8张图片</span>
                    </Upload>
                    <div class="imgs">
                        <div v-for="item in files" :key="">
                            <img :src="item.url" width="58px" height="58px">   
                            <div @click="deleteImg(item.index)"> 
                                <Icon type="close-circled" style="position: relative;top: -50px;left: -10px;cursor: pointer;"></Icon> 
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="reportGood_btn">
                <Button type="primary" @click="reportOrder">确认举报</Button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            pages: 0,
            order_columns: [{
                title: '标题',
                key: 'title',
                width: 270
            },{
                title: '日期',
                key: 'creatTime',
                width: 150
            },{
                title: '数量',
                key: 'goodsNum',
                width: 100
            },{
                title: '总价',
                key: 'goodsTotal',
                width: 100
            },{
                title: '操作',
                key: 'action',
                width: 130,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'error',
                                size: 'small',
                                 
                            },
                            on: {
                                click: ()=>{
                                    this.onff = false;
                                    this.detailOrder = params.row;
                                    this.detailOrder.index = params.index;
                                }
                            }
                        },'发起举报')
                    ]);
                }
            }],
            orderData: [],
            onff: true,
            detailOrder: {},
            reason: '',
            files: []
        }
    },
    created: function(){
        this.getOrderList(1);
    },
    methods: {
        //举报函数
        reportOrder(){
            let self = this;
            let form = new FormData();
            form.append('userId',localStorage.id);
            form.append('orderId',self.detailOrder.id);
            form.append('reason',self.reason);
            this.files.forEach(e1 => {
                form.append('pubishPic',e1);
            });
            this.$http(process.env.API_HOST+'report/post',{
                method: 'post',
                data: form,
                headers: {
                    "Content-Type": "application/x-www-form-urlencoded"
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.files = [];
                    self.reason = '';
                    self.$Message.info('成功提交举报！');
                    self.getOrderList(1);
                    self.onff = true;
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }else{
                    self.$Message.error(res.data.msg);
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //删除图片
        deleteImg(index){
            this.files.splice(index,1);
            for(let i=0;i<this.files.length;i++){
                this.files[i].index = i;
            }
        },
        //上传前处理函数
        handleBeforeUpload(file){
            if(this.files.length < 8){
                let self =this;
                let reader = new FileReader();
                reader.readAsDataURL(file);
                const _this = this;
                reader.onloadend = function(e){
                    file.url = reader.result;
                    file.index = self.files.length;
                    self.files.push(file);
                }
            }else{
                this.$Message.info('最多只能上传8张图片');
            }
            return false;
        },
        //获取订单列表
        getOrderList(page){
            let self = this;
            this.$http(process.env.API_HOST+'orders/listSuccessful',{
                method: 'get',
                params: {
                    'page': page,
                    'rows': 10
                }
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data.rows;
                    self.orderData = res.data.data.rows;                    
                    self.orderData.forEach(e1 => {
                        let time = e1.creatTime.split(' ');
                        e1.creatTime = time[0];
                        e1.totalPrice = e1.goods
                    })
                    self.pages = res.data.data.total;
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //页码改变
        pageChange(page){
            this.getOrderList(page);
        },
        //返回函数
        back(){
            this.onff = true;
        }
    }
}
</script>
<style lang="less" scoped>
.newReport{
   .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    } 
    .reportGood{
        margin-top: 25px;
        overflow: hidden;
        label{
            display: inline-block;
            width: 80px;
            float: left;
            font-weight: bold;
        }
        .upload{
            float: left;
            display: inline-block;
            .imgs{
                height: 120px;
                div{
                    display: inline-block;
                }
            }
            .camera{
                background: rgba(0,0,0,.3);
                width: 58px;
                height:58px;
                line-height: 58px;
                margin-bottom: 15px;
                cursor: pointer;
                i{
                    margin-left: 20px;
                }
            }
        }
        
    }
    .back_btn{
        color: #f5866e;
        font-size: 16px;
        cursor: pointer;
        position: relative;
        top: -35px;
        left: 650px;
    }
    .back_btn:hover{
        text-decoration: underline;
    }
    
    .reportGood_btn{
        margin-left: 80px;
        margin-top: 50px;
    }
    
}
</style>



