<template>
    <div class="reportHistory">
        <p class="p_head">当前位置--举报历史</p>
        <div v-if="onff" style="width: 860px">
            <Table height='615' style="margin-right: 100px" :columns="order_columns" :data="orderData"></Table>
            <Page :total="pages" :page-size="10" @on-change="pageChange"></Page>
        </div>

        <div v-else>
            <span @click="back" class="back_btn">返回</span>
            <div class="reportGood">
                <label>商品标题</label>
                <span>{{ detailInfo.orderDto.title }}</span>
            </div>
            <div class="reportGood">
                <label>举报原因</label>
                <span>{{ detailInfo.reason }}</span>
            </div>
            <div class="reportGood">
                <label>举报日期</label>
                <span>{{ detailInfo.reportTime }}</span>
            </div>
            <div class="reportGood">
                <label>审核意见</label>
                <span>{{ detailInfo.checkResult }}</span>
            </div>
            <div class="reportGood">
                <label>图片</label>
                <dl>
                    <dd v-for="item in detailInfo.picArray" :key="">
                        <img :src="item" width="150px" height="150px">
                    </dd>
                </dl>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            pages: 0,
            order_columns: [{
                title: '标题',
                key: 'title',
                width: 325
            },{
                title: '状态',
                key: 'checkStatus',
                width: 150
            },{
                title: '日期',
                key: 'reportTime',
                width: 150
            },{
                title: '操作',
                key: 'action',
                width: 130,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'primary',
                                size: 'small',
                            },
                            on: {
                                click: ()=>{
                                    this.onff = false;
                                    this.detailInfo = params.row;
                                    console.log(this.detailInfo);
                                    console.log(params.row);
                                }
                            }
                        },'查看详情')
                    ]);
                }
            }],
            orderData: [],
            onff: true,
            detailInfo: {},
            reason: '',
            files: []
        }
    },
    created: function(){
        this.getRHistory(1);
    },
    watch: {
        '$route'(to,form){
            if(to.name == 'reportHistory'){
                this.getRHistory(1);
                this.onff = true;                
            }
        }
    },
    methods: {
        //获取举报列表
        getRHistory(page){
            let self = this;
            this.$http(process.env.API_HOST+'report/getMyReport',{
                method: 'get',
                params: {
                    'page': page,
                    'row': 10
                }
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data.rows;
                    self.orderData = res.data.data.rows;                    
                    self.orderData.forEach(e1 => {
                        let time = e1.reportTime.split(' ');
                        e1.reportTime = time[0];
                        switch(e1.checkStatus){
                            case 1: e1.checkStatus = '已举报';break;
                            case 2: e1.checkStatus = '审核通过';break;
                            case 3: e1.checkStatus = '审核不通过';break;
                            default: e1.checkStatus = '取消举报';
                        }
                        e1.title = e1.orderDto.title;
                    })
                    self.pages = res.data.data.total;
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //页码改变
        pageChange(page){
            this.getRHistory(page);
        },
        //返回函数
        back(){
            this.onff = true;
        }
    }
}
</script>
<style lang="less" scoped>
.reportHistory{
   .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    } 
    .reportGood{
        margin-top: 25px;
        overflow: hidden;
        label{
            display: inline-block;
            width: 80px;
            float: left;
            font-weight: bold;
        }
        .upload{
            float: left;
            display: inline-block;
            .imgs{
                height: 120px;
                div{
                    display: inline-block;
                }
            }
            .camera{
                background: rgba(0,0,0,.3);
                width: 58px;
                height:58px;
                line-height: 58px;
                margin-bottom: 15px;
                cursor: pointer;
                i{
                    margin-left: 20px;
                }
            }
        }
        
    }
    .reportGood{
            overflow: hidden;
            margin-bottom: 15px;
        label{
            font-weight: bold;
            display: inline-block;
            width: 80px;
            float: left;
        }
        dl{
            display: inline-block;
            dd{
                float: left;
                margin-right: 15px;
            }
        }
    }
    .back_btn{
        color: #f5866e;
        font-size: 16px;
        cursor: pointer;
        position: relative;
        top: -35px;
        left: 650px;
    }
    .back_btn:hover{
        text-decoration: underline;
    }
    .reportGood_btn{
        margin-left: 80px;
        margin-top: 50px;
    }
    
}
</style>



