pper{\n    > .@{spin-prefix-cls}-fix{\n        border: 1px solid @border-color-base;\n        border-top: 0;\n        border-left: 0;\n    }\n}\n\n@keyframes ani-spin-bounce {\n    0% {\n        transform: scale(0);\n    }\n\n    100% {\n        transform: scale(1);\n        opacity: 0;\n    }\n}\n","@alert-prefix-cls: ~\"@{css-prefix}alert\";\n@icon-prefix-cls: ~\"@{css-prefix}icon\";\n\n.@{alert-prefix-cls}{\n    position: relative;\n    padding: 8px 48px 8px 16px;\n    border-radius: @border-radius-base;\n    color: @text-color;\n    font-size: @font-size-small;\n    line-height: 16px;\n    margin-bottom: 10px;\n\n    &&-with-icon{\n        padding: 8px 48px 8px 38px;\n    }\n\n    &-icon {\n        font-size: @font-size-base;\n        top: 8px;\n        left: 16px;\n        position: absolute;\n    }\n\n    &-desc {\n        font-size: @font-size-small;\n        color: @text-color;\n        line-height: 21px;\n        display: none;\n        text-align: justify;\n    }\n\n    &-success {\n        border: 1px solid tint(@success-color, 80%);\n        background-color: tint(@success-color, 90%);\n        .@{alert-prefix-cls}-icon {\n            color: @success-color;\n        }\n    }\n\n    &-info {\n        border: 1px solid tint(@primary-color, 80%);\n        background-color: tint(@primary-color, 90%);\n        .@{alert-prefix-cls}-icon {\n            color: @primary-color;\n        }\n    }\n\n    &-warning {\n        border: 1px solid tint(@warning-color, 80%);\n        background-color: tint(@warning-color, 90%);\n        .@{alert-prefix-cls}-icon {\n            color: @warning-color;\n        }\n    }\n\n    &-error {\n        border: 1px solid tint(@error-color, 80%);\n        background-color: tint(@error-color, 90%);\n        .@{alert-prefix-cls}-icon {\n            color: @error-color;\n        }\n    }\n\n    &-close {\n        .content-close(-3px);\n    }\n\n    &-with-desc {\n        padding: 16px;\n        position: relative;\n        border-radius