     }\n            &-warning {\n                color: @warning-color;\n            }\n            &-error {\n                color: @error-color;\n            }\n            &-confirm {\n                color: @warning-color;\n            }\n        }\n    }\n\n    &-footer{\n        margin-top: 40px;\n        text-align: right;\n\n        button + button {\n            margin-left: 8px;\n            margin-bottom: 0;\n        }\n    }\n}",".mask() {\n    position: fixed;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    background-color: rgba(55, 55, 55, 0.6);\n    height: 100%;\n    z-index: @zindex-modal;\n\n    &-hidden {\n        display: none;\n    }\n}","@select-prefix-cls: ~\"@{css-prefix}select\";\n@select-item-prefix-cls: ~\"@{css-prefix}select-item\";\n@select-group-prefix-cls: ~\"@{css-prefix}select-group\";\n\n.@{select-prefix-cls} {\n    display: inline-block;\n    width: 100%;\n    box-sizing: border-box;\n    vertical-align: middle;\n    color: @text-color;\n    font-size: @font-size-base;\n    //position: relative;\n    line-height: normal;\n\n    &-selection {\n        display: block;\n        box-sizing: border-box;\n        outline: none;\n        user-select: none;\n        cursor: pointer;\n        position: relative;\n\n        background-color: #fff;\n        border-radius: @btn-border-radius;\n        border: 1px solid @border-color-base;\n        transition: all @transition-time @ease-in-out;\n\n        .@{select-prefix-cls}-arrow:nth-of-type(1) {\n            display: none;\n            cursor: pointer;\n        }\n\n        &:hover {\n            .hover();\n            .@{select-prefix-cls}-arrow:nth-of-type(1) {\n                display: inline-block;\n            }\n        }\n    }\n\n    &-show-clear &-selection:hover .@{select-prefix-cls}-arrow:nth-of-type(2){\n        display: none;\n    }\n\n    &-arrow {\n        .inner-arrow();\n    }\n\n    &-visible{\n        .@{select-prefix-cls}-selection{\n            .active(