ct-prefix-cls}-placeholder, .@{select-prefix-cls}-selected-value{\n            height: @input-height-large - 2px;\n            line-height: @input-height-large - 2px;\n            font-size: @font-size-base;\n        }\n    }\n\n    &-large&-multiple &-selection{\n        min-height: @input-height-large;\n\n        .@{select-prefix-cls}-placeholder, .@{select-prefix-cls}-selected-value{\n            min-height: @input-height-large - 2px;\n            line-height: @input-height-large - 2px;\n            font-size: @font-size-base;\n        }\n    }\n\n    &-small&-single &-selection{\n        height: @input-height-small;\n        border-radius: @btn-border-radius-small;\n\n        .@{select-prefix-cls}-placeholder, .@{select-prefix-cls}-selected-value{\n            height: @input-height-small - 2px;\n            line-height: @input-height-small - 2px;\n        }\n    }\n\n    &-small&-multiple &-selection{\n        min-height: @input-height-small;\n        border-radius: @btn-border-radius-small;\n\n        .@{select-prefix-cls}-placeholder, .@{select-prefix-cls}-selected-value{\n            height: auto;\n            min-height: @input-height-small - 2px;\n            line-height: @input-height-small - 2px;\n        }\n    }\n\n    // input\n    &-input{\n        display: inline-block;\n        height: @input-height-base;\n        line-height: @input-height-base;\n        padding: 0 24px 0 8px;\n        font-size: @font-size-small;\n        outline: none;\n        border: none;\n        box-sizing: border-box;\n        color: @input-color;\n        background-color: transparent;\n        position: relative;\n        cursor: pointer;\n        .placeholder();\n\n        &[disabled]{\n            cursor: @cursor-disabled;\n            color: #ccc;\n        }\n    }\n\n    &-single &-input{\n        width: 100%;\n    }\n\n    &-large &-input{\n        font-size: @font-size-base;\n        height: @input-height-large;\n    }\n\n    &-small &-input{\n        height: @input-height-small - 2px;\n        line-height: @input-height-small - 2px;\n    }\n\n    &-multiple &-input{\n        height: @input-height-base - 3px;\n        line-height: @input-height-base;\n        padding: 0 0 0 4px;\n    }\n\n    &-not-found{\n        text-align: center;\n        color: @btn-disable-color;\n        li:not([class^=ivu-]){\n            margin-bottom: 0;\n        }\n    }\n    &-loading{\n        text-align: center;\n        color: @btn-disable-color;\n    }\n\n    &-multiple .@{css-prefix}tag{\n        height: 24px;\n        line-height: 22px;\n        margin: 3px 4px 3px 0;\n    }\n\n    &-large&-multiple .@{css-prefix}tag{\n        height: 28px;\n        line-height: 26px;\n        font-size: @font-size-base;\n    }\n\n    &-small&-multiple .@{css-prefix}tag{\n        height: 17px;\n        line-height: 15px;\n        font-size: @font-size-small;\n        padding: 0 6px;\n        margin: 3px 4px 2px 0;\n    }\n\n    &-dropdown-list {\n        display: inline-block;\n        min-width: 100%;\n        list-style: none;\n    }\n}\n\n.select-item(@select-prefix-cls, @select-item-prefix-cls);\n\n.@{select-prefix-cls}-multiple .@{select-item-prefix-cls} {\n    &-selected{\n        color: @selected-color;\n        background: #fff;\n    }\n    &-focus,&-selected:hover{\n        background: @background-color-select-hover;\n    }\n\n    &-selected&-focus {\n        color: shade(@selected-color, 10%);\n        background: #fff;\n    }\n\n    &-selected:after{\n        .ivu-icon();\n        float: right;\n        font-size: 24px;\n        content: '\\F3FD';\n        color: @selected-color;\n    }\n}\n\n.@{select-group-prefix-cls} {\n    list-style: none;\n    margin: 0;\n    padding: 0;\n\n    &-title {\n        padding-left: 8px;\n        font-size: 12px;\n        color: @legend-color;\n        height: 30px;\n        line-height: 30px;\n    }\n}\n\n.@{form-item-prefix-cls}-error{\n    .@{select-prefix-cls}{\n        &-selection{\n            border: 1px solid @error-color;\n        }\n        &-arrow{\n            color: @error-color;\n        }\n        &-visible .@{select-prefix-cls}-selection{\n            .active-error;\n        }\n    }\n}\n",".select-item(@size-class, @item-class) {\n    .@{item-class} {\n        margin: 0;\n        line-height: normal;\n        padding: 7px 16px;\n        clear: both;\n        color: @text-color;\n        font-size: @font-size-small !important;\n        white-space: nowrap;\n        list-style: none;\n        cursor: pointer;\n        transition: background @transition-time @ease-in-out;\n\n        &:hover{\n            background: @background-color-select-hover;\n        }\n\n        &-focus {\n            background: @background-color-select-hover;\n        }\n\n        &-disabled {\n            color: @btn-disable-color;\n            cursor: @cursor-disabled;\n\n            &:hover {\n                color: @btn-disable-color;\n                background-color: #fff;\n                cursor: @cursor-disabled;\n            }\n        }\n\n        &-selected ,&-selected:hover{\n            color: #fff;\n            background: @selected-color;\n        }\n\n        &-selected&-focus {\n            background: shade(@selected-color, 10%);\n        }\n\n        &-divided{\n            margin-top: 5px;\n            border-top: 1px solid @border-color-split;\n            &:before{\n                content: '';\n                height: 5px;\n                display: block;\n                margin: 0 -16px;\n                background-color: #fff;\n                position: relative;\n                top: -7px;\n            }\n        }\n    }\n\n    .@{size-class}-large .@{item-class} {\n        padding: 7px 16px 8px;\n        font-size: @font-size-base !important;\n    }\n    // http://browserhacks.com/\n    // https://bugzilla.mozilla.org/show_bug.cgi?id=488725\n    // fixed #1224 #1143 #1127\n    @-moz-document url-prefix() {\n        .@{item-class} {\n            white-space: normal;\n        }\n    }\n}\n","@select-dropdown-prefix-cls: ~\"@{css-prefix}select-dropdown\";\n@transfer-no-max-height: ~\"@{css-prefix}transfer-no-max-height\";\n\n.@{select-dropdown-prefix-cls} {\n    width: inherit;\n    max-height: 200px;\n    overflow: auto;\n    margin: 5px 0;\n    padding: 5px 0;\n    background-color: #fff;\n    box-sizing: border-box;\n    //border: 1px solid @border-color-split;\n    border-radius: @btn-border-radius;\n    //box-shadow: 0 1px 3px rgba(0,0,0,.2);\n    box-shadow: @shadow-base;\n    position: absolute;\n    z-index: @zindex-select;\n    &-transfer{\n        z-index: @zindex-transfer;\n    }\n    &.@{transfer-no-max-height} {\n        max-height: none;\n    }\n}\n.@{modal-prefix-cls} {\n    .@{select-dropdown-prefix-cls} {\n        position: absolute !important;\n    }\n}","@tooltip-prefix-cls: ~\"@{css-prefix}tooltip\";\n@tooltip-arrow: ~\"@{tooltip-prefix-cls}-arrow\";\n@tooltip-max-width: 250px;\n@tooltip-arrow-width: 5px;\n@tooltip-distance: @tooltip-arrow-width - 1 + 4;\n\n.@{tooltip-prefix-cls} {\n    display: inline-block;\n\n    &-rel{\n        display: inline-block;\n        position: relative;\n    }\n\n    &-popper{\n        .popper(@tooltip-arrow, @tooltip-arrow-width, @tooltip-distance, @tooltip-bg);\n    }\n\n    &-inner{\n        max-width: @tooltip-max-width;\n        min-height: 34px;\n        padding: 8px 12px;\n        color: @tooltip-color;\n        text-align: left;\n        text-decoration: none;\n        background-color: @tooltip-bg;\n        border-radius: @border-radius-small;\n        box-shadow: @shadow-base;\n        white-space: nowrap;\n    }\n\n    &-arrow{\n        position: absolute;\n        width: 0;\n        height: 0;\n        border-color: transparent;\n        border-style: solid;\n    }\n}",".popper(@arrow, @arrow-width, @arrow-distance, @bg){\n    display: block;\n    visibility: visible;\n    font-size: @font-size-small;\n    line-height: @line-height-base;\n    position: absolute;\n    z-index: @zindex-tooltip;\n\n    &[x-placement^=\"top\"] {\n        padding: @arrow-width 0 @arrow-distance 0;\n    }\n    &[x-placement^=\"right\"] {\n        padding: 0 @arrow-width 0 @arrow-distance;\n    }\n    &[x-placement^=\"bottom\"] {\n        padding: @arrow-distance 0 @arrow-width 0;\n    }\n    &[x-placement^=\"left\"] {\n        padding: 0 @arrow-distance 0 @arrow-width;\n    }\n\n    &[x-placement^=\"top\"] .@{arrow} {\n        bottom: @arrow-distance - @arrow-width;\n        border-width: @arrow-width @arrow-width 0;\n        border-top-color: @bg;\n    }\n    &[x-placement=\"top\"] .@{arrow} {\n        left: 50%;\n        margin-left: -@arrow-width;\n    }\n    &[x-placement=\"top-start\"] .@{arrow} {\n        left: 16px;\n    }\n    &[x-placement=\"top-end\"] .@{arrow} {\n        right: 16px;\n    }\n\n    &[x-placement^=\"right\"] .@{arrow} {\n        left: @arrow-distance - @arrow-width;\n        border-width: @arrow-width @arrow-width @arrow-width 0;\n        border-right-color: @bg;\n    }\n    &[x-placement=\"right\"] .@{arrow} {\n        top: 50%;\n        margin-top: -@arrow-width;\n    }\n    &[x-placement=\"right-start\"] .@{arrow} {\n        top: 8px;\n    }\n    &[x-placement=\"right-end\"] .@{arrow} {\n        bottom: 8px;\n    }\n\n    &[x-placement^=\"left\"] .@{arrow} {\n        right: @arrow-distance - @arrow-width;\n        border-width: @arrow-width 0 @arrow-width @arrow-width;\n        border-left-color: @bg;\n    }\n    &[x-placement=\"left\"] .@{arrow} {\n        top: 50%;\n        margin-top: -@arrow-width;\n    }\n    &[x-placement=\"left-start\"] .@{arrow} {\n        top: 8px;\n    }\n    &[x-placement=\"left-end\"] .@{arrow} {\n        bottom: 8px;\n    }\n\n    &[x-placement^=\"bottom\"] .@{arrow} {\n        top: @arrow-distance - @arrow-width;\n        border-width: 0 @arrow-width @arrow-width;\n        border-bottom-color: @bg;\n    }\n    &[x-placement=\"bottom\"] .@{arrow} {\n        left: 50%;\n        margin-left: -@arrow-width;\n    }\n    &[x-placement=\"bottom-start\"] .@{arrow} {\n        left: 16px;\n    }\n    &[x-placement=\"bottom-end\"] .@{arrow} {\n        right: 16px;\n    }\n}","@poptip-prefix-cls: ~\"@{css-prefix}poptip\";\n@poptip-arrow: ~\"@{poptip-prefix-cls}-arrow\";\n@poptip-max-width: 250px;\n@poptip-arrow-width: 5px;\n@poptip-arrow-outer-width: (@poptip-arrow-width + 1);\n@poptip-distance: @poptip-arrow-width - 1 + 4;\n//@poptip-arrow-color: fadein(@border-color-base, 5%);\n@poptip-arrow-color: hsla(0,0%,85%,.5);\n\n.@{poptip-prefix-cls} {\n    display: inline-block;\n\n    &-rel{\n        display: inline-block;\n        position: relative;\n    }\n\n    &-title {\n        margin: 0;\n        padding: 8px 16px;\n        position: relative;\n\n        &:after{\n            content: '';\n            display: block;\n            height: 1px;\n            position: absolute;\n            left: 8px;\n            right: 8px;\n            bottom: 0;\n            background-color: @border-color-split;\n        }\n\n        &-inner{\n            color: @title-color;\n            font-size: @font-size-base;\n        }\n    }\n\n    &-body{\n        padding: 8px 16px;\n\n        &-content{\n            overflow: auto;\n\n            &-inner{\n                color: @text-color;\n            }\n        }\n    }\n\n    &-inner{\n        width: 100%;\n        background-color: #fff;\n        background-clip: padding-box;\n        //border: 1px solid @border-color-split;\n        border-radius: @border-radius-small;\n        box-shadow: @shadow-base;\n        white-space: nowrap;\n    }\n\n    &-popper{\n        min-width: 150px;\n        font-size: @font-size-small;\n        .popper(@poptip-arrow, @poptip-arrow-width, @poptip-distance, @poptip-arrow-color);\n\n        &[x-placement^=\"top\"] .@{poptip-arrow}:after {\n            content: \" \";\n            bottom: 1px;\n            margin-left: -@poptip-arrow-width;\n            border-bottom-width: 0;\n            border-top-color: #fff;\n        }\n\n        &[x-placement^=\"right\"] .@{poptip-arrow}:after {\n            content: \" \";\n            left: 1px;\n            bottom: -@poptip-arrow-width;\n            border-left-width: 0;\n            border-right-color: #fff;\n        }\n\n        &[x-placement^=\"bottom\"] .@{poptip-arrow}:after {\n            content: \" \";\n            top: 1px;\n            margin-left: -@poptip-arrow-width;\n            border-top-width: 0;\n            border-bottom-color: #fff;\n        }\n\n        &[x-placement^=\"left\"] .@{poptip-arrow}:after {\n            content: \" \";\n            right: 1px;\n            border-right-width: 0;\n            border-left-color: #fff;\n            bottom: -@poptip-arrow-width;\n        }\n    }\n\n    &-arrow{\n        &, &:after{\n            display: block;\n            width: 0;\n            height: 0;\n            position: absolute;\n            border-color: transparent;\n            border-style: solid;\n        }\n    }\n    &-arrow {\n        border-width: @poptip-arrow-outer-width;\n    }\n    &-arrow:after{\n        content: \"\";\n        border-width: @poptip-arrow-width;\n    }\n\n    &-confirm &-popper{\n        max-width: 300px;\n    }\n    &-confirm &-inner{\n        white-space: normal;\n    }\n\n    &-confirm &-body{\n        padding: 16px 16px 8px;\n        .ivu-icon{\n            font-size: 16px;\n            color: @warning-color;\n            line-height: 18px;\n            position: absolute;\n        }\n\n        &-message{\n            padding-left: 20px;\n        }\n    }\n\n    &-confirm &-footer{\n        text-align: right;\n        padding: 8px 16px 16px;\n        button {\n            margin-left: 4px;\n        }\n    }\n}","@input-prefix-cls: ~\"@{css-prefix}input\";\n\n.@{input-prefix-cls} {\n    .input;\n    &-wrapper{\n        display: inline-block;\n        width: 100%;\n        position: relative;\n        vertical-align: middle;\n\n        // #2149 & #2219\n