e(2) {\n        transform: rotate(180deg);\n    }\n\n    .@{select-dropdown-prefix-cls} {\n        .selectDropDown();\n    }\n\n    .select-item(@cascader-prefix-cls, @cascader-item-prefix-cls);\n    .ivu-select-item span{\n        color: @error-color;\n    }\n\n    &-dropdown{\n        padding: 5px 0;\n        .@{select-dropdown-prefix-cls}-list{\n            max-height: 190px;\n            box-sizing: border-box;\n            overflow: auto;\n        }\n    }\n\n    &-not-found-tip{\n        padding: 5px 0;\n        text-align: center;\n        color: @btn-disable-color;\n        li:not([class^=ivu-]){\n            list-style: none;\n            margin-bottom: 0;\n        }\n    }\n    &-not-found .@{select-dropdown-prefix-cls} {\n        width: inherit;\n    }\n\n    &-menu{\n        display: inline-block;\n        min-width: 100px;\n        height: 180px;\n        margin: 0;\n        padding: 5px 0 !important;\n        vertical-align: top;\n        list-style: none;\n        border-right: 1px solid @border-color-split;\n        overflow: auto;\n\n        &:first-child {\n\n        }\n        &:last-child {\n            border-right-color: transparent;\n            margin-right: -1px;\n        }\n        &:only-child {\n\n        }\n\n        & &-item{\n            position: relative;\n            padding-right: 24px;\n            transition: all @transition-time @ease-in-out;\n\n            i{\n                font-size: @font-size-small;\n                position: absolute;\n                right: 15px;\n                top: 50%;\n                margin-top: -6px;\n            }\n\n            &-active{\n                background-color: @background-color-select-hover;\n                color: @primary-color;\n            }\n        }\n    }\n}\n.@{cascader-prefix-cls}-transfer{\n    z-index: @zindex-transfer;\n    .selectDropDown();\n    .select-item(@cascader-prefix-cls, @cascader-item-prefix-cls);\n    .ivu-select-item span{\n        color: @error-color;\n   