p: normal;\n        vertical-align: middle;\n    }\n\n    td&-expanded-cell{\n        padding: 20px 50px;\n        background: @table-thead-bg;\n    }\n\n    &-stripe &-body,\n    &-stripe &-fixed-body\n    {\n        tr:nth-child(2n) {\n            td{\n                background-color: @table-td-stripe-bg;\n            }\n        }\n        // #1380\n        tr.@{table-prefix-cls}-row-hover{\n            td{\n                background-color: @table-td-hover-bg;\n            }\n        }\n    }\n\n    tr&-row-hover{\n        td{\n            background-color: @table-td-hover-bg;\n        }\n    }\n\n    &-large {\n        font-size: @font-size-base;\n        th{\n            height: 48px;\n        }\n        td{\n            height: 60px;\n        }\n        &-title, &-footer{\n            height: 60px;\n            line-height: 60px;\n        }\n        .@{table-prefix-cls}-cell-with-expand{\n            height: 59px;\n            line-height: 59px;\n            i{\n                font-size: @font-size-base+2;\n            }\n        }\n    }\n\n    &-small{\n        th{\n            height: 32px;\n        }\n        td{\n            height: 40px;\n        }\n        &-title, &-footer{\n            height: 40px;\n            line-height: 40px;\n        }\n        .@{table-prefix-cls}-cell-with-expand{\n            height: 39px;\n            line-height: 39px;\n        }\n    }\n\n    &-row-highlight,\n    tr&-row-highlight&-row-hover,\n    &-stripe &-body tr&-row-highlight:nth-child(2n),\n    &-stripe &-fixed-body tr&-row-highlight:nth-child(2n)\n    {\n        td{\n            background-color: @table-td-highlight-bg;\n        }\n    }\n\n    &-fixed, &-fixed-right{\n        position: absolute;\n        top: 0;\n        left: 0;\n        box-shadow: 2px 0 6px -2px rgba(0, 0, 0, 0.2);\n\n        &::before {\n            content: '';\n            width: 100%;\n            height: 1px;\n            background-color: @border-color-base;\n            position: absolute;\n            left: 0;\n            bottom: 0;\n            z-index: 4;\n        }\n    }\n    &-fixed-right{\n        top: 0;\n        left: auto;\n        right: 0;\n        box-shadow: -2px 0 6px -2px rgba(0, 0, 0, 0.2);\n    }\n    &-fixed-right-header{\n        position: absolute;\n        top: -1px;\n        right: 0;\n        background-color: @table-thead-bg;\n        border-top: 1px solid @border-color-base;\n        border-bottom: 1px solid @border-color-split;\n    }\n    &-fixed-header{\n        overflow: hidden;\n        &-with-empty{\n            .@{table-prefix-cls}-hidden{\n                .@{table-prefix-cls}-sort{\n                    display: none;\n                }\n                .@{table-prefix-cls}-cell span{\n                    display: none;\n                }\n            }\n        }\n    }\n    &-fixed-body{\n        overflow: hidden;\n        position: relative;\n        z-index: 3;\n    }\n\n    &-fixed-shadow {\n        width: 1px;\n        height: 100%;\n        position: absolute;\n        top: 0;\n        right: 0;\n        box-shadow: @shadow-right;\n        overflow: hidden;\n        z-index: 1;\n    }\n\n    &-sort{\n        .sortable();\n    }\n    &-filter{\n        display: inline-block;\n        cursor: pointer;\n        position: relative;\n\n        i{\n            color: @btn-disable-color;\n            transition: color @transition-time @ease-in-out;\n            &:hover{\n                color: inherit;\n            }\n            &.on{\n                color: @primary-color;\n            }\n        }\n        &-list{\n            padding: 8px 0 0;\n            &-item{\n                padding: 0 12px 8px;\n\n                .ivu-checkbox-wrapper + .ivu-checkbox-wrapper{\n                    margin: 0;\n                }\n                label {\n                    display: block;\n\n                    & > span{\n                        margin-right: 4px;\n                    }\n                }\n            }\