<template>
    <div class="person">
        <img class="img" src="../assets/05.jpg" width="100%">
        <div class="person_content">
            <div class="menu">
                <Menu theme="light" :accordion='true' @on-select="handleSelect">
                    <Submenu name="1">
                        <template slot="title">
                            订单管理
                        </template>
                        <!-- <MenuItem name="1-1">发起订单</MenuItem> -->
                        <MenuItem name="deleteOrder">订单管理（买家）</MenuItem>
                        <MenuItem name="confirmUserOrder">确认订单(买家)</MenuItem>                        
                        <!-- <MenuItem name="1-3">修改订单</MenuItem> -->
                        <MenuItem name="orderHistory">订单历史</MenuItem>
                        <MenuItem name="confirmOrder">确认订单(卖家)</MenuItem>
                    </Submenu>
                    <Submenu name="2">
                        <template slot="title">
                            举报管理
                        </template>
                        <MenuItem name="newReport">举报商品</MenuItem>
                        <MenuItem name="cancelReport">取消举报</MenuItem>
                        <MenuItem name="reportHistory">举报历史</MenuItem>
                    </Submenu>
                    <!-- <Submenu name="2">
                        <template slot="title">
                            个人信息
                        </template>
                        <MenuItem name="personData">修改信息</MenuItem>
                        <!- <MenuItem name="2-2">修改密码</MenuItem> ->
                    </Submenu> -->
                        <MenuItem name="personData">修改信息</MenuItem>
                        <MenuItem name="collection">收藏夹</MenuItem>
                    
                    <Submenu name="3">
                        <template slot="title">
                            发布信息
                        </template>
                        <MenuItem name="publishInfo" >发布信息</MenuItem>
                        <MenuItem name="modifyPublish">修改信息</MenuItem>
                        <MenuItem name="deletePublish">删除信息</MenuItem>
                        <!-- <MenuItem name="publishHistory">历史信息</MenuItem> -->
                    </Submenu>
                </Menu>
            </div>

            <div class="view">
                <keep-alive>
                    <router-view></router-view>
                </keep-alive>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            
        }
    },
    methods: {
        handleSelect(cvalue){
            this.$router.push({path: cvalue});
        }
    }
}
</script>

<style lang="less" scoped>

    .person{
        .img{
            min-width: 1400px;
        }
        .person_content{
            // margin-top: 30px;
            overflow: hidden;
            width: 1100px;
            height: 800px;
            margin: 30px auto 0 auto;
            // padding: 35px;
            div{
                display: inline-block;         
                float: left;     
                // border: 1px solid #ddd;                  
            }
            .view{
                margin-left: 15px;
                padding: 30px 40px;
                height: 800px;
                background: #f3f3f3;
                width: 840px;
                float: left;
            }
        }
    }

</style>



