<template>
    <div class="top">
        <div class="top_content">
            <img src="../assets/02.png" style="cursor: pointer;float:left;" width="95px" @click="backHome">
            <div style="display: inline-block;float: left;margin-top: 9px;margin-left: 50px;">
                <Select v-model="search_type" style="width: 70px;" size="small">
                    <Option v-for="item in search_typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                </Select>
                <Input type="text" style="width: 200px;" v-model.trim="search"></Input>
                <Button type="primary" shape="circle" icon="ios-search" @click="searchBtn"></Button>
            </div>
            <dl>
                <dd v-if="nameonff">{{ usersname }} ,你好 [ <span @click="modal2 = true">修改密码</span> | <span @click="exit">退出</span> ]</dd>                
                <dd v-else><span @click="modal1 = true">登录</span> | <span @click="modal3 = true">注册</span></dd>
                <dd><span @click="person">个人中心</span></dd>
            </dl>
        </div>

        <div>
            <Modal v-model="modal1" title="登录" @on-ok="login" @on-cancel="cancel">
                <div style="margin-left: 90px;">
                    <label>账&nbsp;&nbsp;&nbsp;&nbsp;号</label>
                    <Input style="width: 250px;" type="text" v-model.trim="username" placeholder="请输入用户名"></Input>
                </div>
                <div style="margin: 30px 0 30px 90px;">
                    <label>密&nbsp;&nbsp;&nbsp;&nbsp;码</label>
                    <Input style="width: 250px;" type="password" v-model.trim="password" placeholder="请输入密码"></Input>
                </div>
                <!-- <div style="margin-left: 160px;">
                    <Select v-model="tType" @on-change="handleTabel">
                        <Option v-for="item in tabelList" :key="item.value" :value="item.value">{{ item.label }}</Option>
                    </Select> -->
                    <!-- <RadioGroup v-model="tType">
                        <Radio label="admin">
                            <span style="font-size: 16px;margin-right: 55px;">管理员</span>
                        </Radio>
                        <Radio label="user">
                            <span style="font-size: 16px;">用户</span>
                        </Radio>
                    </RadioGroup>
                </div> -->
                <!-- <div style="margin-left: 90px;">
                    <label>验证码</label>
                    <Input type="text" v-model.trim="code" placeholder="请输入验证码"></Input>
                    <div style="overflow: hidden;display: inline-block;marin-left: 200px;">
                        <img id="codeimg" :src="imgsrc" style="float: left;height: 35px;line-height: 35px;">
                        <span @click="imgsCode" style="color: #2255DD;cursor: pointer;font-size: 14px;float: left;height: 50px;line-height: 50px;">换一张</span>
                    </div>
                </div> -->
            </Modal>
        </div>
        
        <div>
            <Modal v-model="modal3" title="注册" @on-ok="registered" @on-cancel="rcancel">
                <div style="margin-left: 60px">
                    <label style="display: inline-block;width: 90px;margin-bottom: 30px;">用户名</label>
                    <Input type="text" style="width: 250px;" v-model="register.username" placeholder="请输入用户名"></Input>
                </div>
                <div style="margin-left: 60px">
                    <label style="display: inline-block;width: 90px;margin-bottom: 30px;">密码</label>
                    <Input type="password" style="width: 250px;" v-model.trim="register.password" placeholder="请输入密码"></Input>
                </div>
                <div style="margin-left: 60px">
                    <label style="display: inline-block;width: 90px;margin-bottom: 30px;">联系方式</label>
                    <Input type="text" style="width: 250px;" v-model.trim="register.mobile" placeholder="请输入联系方式"></Input>
                </div>
                <div style="margin-left: 60px">
                    <label style="display: inline-block;width: 90px;margin-bottom: 30px;">邮箱</label>
                    <Input type="text" style="width: 250px;" v-model.trim="register.email" placeholder="请输入邮箱"></Input>
                </div>
                <div style="margin-left: 60px">
                    <label style="display: inline-block;width: 90px;">身份证号码</label>
                    <Input type="text" style="width: 250px;" v-model.trim="register.identity" placeholder="请输入身份证号码"></Input>
                </div>

                <!-- <Form ref="register" :model="register" :rules="registerRule" :label-width="80">
                    <FormItem label="用户名" prop="name">
                        <Input type="text" style="width: 250px;" v-model="register.username" placeholder="请输入用户名"></Input>
                    </FormItem>
                    <FormItem label="密码" prop="name">
                        <Input type="password" style="width: 250px;" v-model.trim="register.password" placeholder="请输入密码"></Input>
                    </FormItem>
                    <FormItem label="联系方式" prop="name">
                        <Input type="text" style="width: 250px;" v-model.trim="register.mobile" placeholder="请输入联系方式"></Input>
                    </FormItem>
                    <FormItem label="邮箱" prop="name">
                        <Input type="text" style="width: 250px;" v-model.trim="register.email" placeholder="请输入邮箱"></Input>
                    </FormItem>
                    <FormItem label="身份证号码" prop="name">
                        <Input type="text" style="width: 250px;" v-model.trim="register.identity" placeholder="请输入身份证号码"></Input>
                    </FormItem>
                </Form> -->
            </Modal>
        </div>

        <div>
            <Modal v-model="modal2" title="修改密码" @on-ok="modifyPw" @on-cancel="PWCancel">
                <div style="margin-left: 70px;">
                    <label>旧密码</label>
                    <Input type='password' style="width: 250px;" v-model.trim="MPassword1"></Input>
                </div>
                <div style="margin-left: 70px;margin-top: 25px;">
                    <label>新密码</label>
                    <Input type="password" style="width: 250px;" v-model.trim="MPassword2"></Input>
                </div>
            </Modal>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            password: '',
            username: '',
            MPassword1: '',
            MPassword2: '',
            modal1: false,
            modal2: false,
            modal3: false,
            userData: {},
            register: {},
            code: '',
            imgcode: new FormData(),
            nameonff: false,
            // tabelList: [{
            //     value: 'admin',
            //     label: '管理员'
            // },{
            //     value: 'person',
            //     label: '用户'
            // }],
            usersname: '',
            tType: 'user',
            search: '',
            search_type: '1',
            search_typeList: [{
                value: '1',
                label: '供应'
            },{
                value: '2',
                label: '需求'
            }]
        }
    },
    computed: {
        // nameonff: function(){
        //     return this.loginstate;
        // },
        // usersname: function(){
        //     return localStorage.userName;
        // },
        // imgsrc: function(){
        //     return "http://119.29.194.197:8080/secondHandMarket-web/user/code?" + Math.random();
        // }
    },
    watch: {
        // username: function(newvalue,oldvalue){
        //     console.log(newvalue);
        //     console.log(oldvalue);
            // this.username = localStorage.userName;
        // }
    },
    created: function(){
        // this.imgsCode();
        // this.nameonff = localStorage.getItem('id')?true:false;
        // console.log(this.$store.state.loginState);
        this.nameonff = localStorage.id?true:false;
        this.usersname = localStorage.userName;
        let self = this;
        window.addEventListener('storage',function(event){
            if(event.key == 'id'){
                let newvalue = event.newValue;
                if(newvalue == null){
                    self.nameonff = false;
                }else{
                    self.nameonff = true;
                    self.usersname = localStorage.userName;               
                }
            }
        },false);
        this.search = this.searchWord;
        this.search = this.$route.query.search;
        if(this.$route.query.status){
            this.search_type = this.$route.query.status;
        }
    },
    props: ['loginstate','searchWord','searchType'],
    methods: {
        //图标的回到首页函数
        backHome(){
            this.$router.push({path: '/'});
        },
        searchBtn(){          
            window.open(window.location.origin+'/secondHandMarket-web/#/browseInfo?type=search&status='+this.search_type+'&search='+this.search);
            
        },
        // imgsCode(){
        //     let img = document.getElementById('codeimg');
        //     img.src = "http://119.29.194.197:8080/secondHandMarket-web/user/code?" + Math.random();
        // },
        // imgsCode(){
        //     let self = this;
        //     this.$http(process.env.API_HOST+'user/code',{
        //         method: 'get',
        //         responseType: 'blob'
        //     }).then(function(res){
        //         let reader = new FileReader();
        //         let file = res;
        //         reader.readAsDataURL(file);
        //         reader.onloadend = function(e){
        //             file.url = reader.result;
        //             self.imgcode = file
        //         }
        //         // self.imgcode = res.data;
        //         // console.log(res);
        //         console.log(self.imgcode);
        //     }).catch(function(err){
        //         console.log(err);
        //     })
        // },
        //取消注册函数
        rcancel(){
            this.$Message.info('已取消注册');
            this.register = {};
        },
        //取消登录函数
        cancel(){
            this.$Message.info('已取消登录');
            this.username = '';
            this.password = '';
        },
        //登录管理员请求函数
        // login_admin(){
        //     let self = this;
        //     this.$http(process.env.API_HOST+'manager/login',{
        //         method: 'post',
        //         data:{
        //             'username': self.username,
        //             'password': self.password
        //         }
        //     }).then(function(res){
        //         if(res.data.status == 200){
        //             // self.$store.state.loginState = true;
        //             self.$emit('lState',true);
        //             localStorage.setItem('loginstate',true);
        //             self.nameonff = true;
        //             let data = res.data.data.data;
        //             self.userData = res.data.data.data;
        //             delete self.userData.password;
        //             delete self.userData.salt;
        //             // self.$store.state.loginState = true;
        //             localStorage.setItem('id',data.id);
        //             localStorage.setItem('userName',data.username);
        //             localStorage.setItem('identity','admin');       
        //             self.usersname = localStorage.userName; 
        //         }else{
        //             self.$Message.error(res.data);
        //         }
        //         self.username = '';
        //         self.password = '';
        //     }).catch(function(err){
        //         self.$Message.error('登录失败！');
        //     })
        // },
        //登录用户请求函数
        login_person(){
            let self = this;
            this.$http(process.env.API_HOST+'user/login',{
                method: 'post',
                data: {
                    'username': self.username,
                    'password': self.password,
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.nameonff = true;
                    // self.$store.state.loginState = true;
                    self.$emit('lState',true);
                    let data = res.data.data;
                    self.userData = res.data.data;
                    delete self.userData.password;
                    delete self.userData.salt;
                    localStorage.setItem('loginstate',true);
                    localStorage.setItem('id',data.id);
                    localStorage.setItem('userName',data.username);
                    localStorage.setItem('identity','user');
                    self.usersname = localStorage.userName;    
                                
                }else{
                    self.$Message.error(res.data.msg);
                }
                self.username = '';
                self.password = '';
            }).catch(function(err){
                self.$Message.error('登录失败！');
            })
        },
        //登录函数
        login(){
            if(this.username != '' && this.password != ''){
                // if(this.tType == 'admin'){
                //     this.login_admin();
                // }else{
                    this.login_person();
                // }
            }else{
                this.$Message.info('请将信息填写完整');
            }
            
        },
        //退出函数
        exit(){
            this.userData = {};
            let self = this;
            // if(localStorage.getItem('identity') == 'user'){
                self.$http(process.env.API_HOST + 'user/cancel',{
                    method: 'get'
                }).then(function(res){
                    localStorage.clear();
                    self.nameonff = false;
                    self.$emit('lState',false);
                    self.username = '';
                    // self.$store.state.loginState = false;                    
                    if(self.$route.path != '/detailInfo'&&self.$route.path != '/'){
                        self.$router.push({path: '/'});
                    }
                }).catch(function(err){
                    console.log(err);
                })
            // }else{
            //     this.$http(process.env.API_HOST + 'manager/logout',{
            //         method: 'delete'
            //     }).then(function(res){
            //         localStorage.clear();
            //         self.nameonff = false;
            //         self.$emit('lState',false);
            //         self.username = '';
            //         if(self.$route.path != '/detailInfo'&&self.$route.path != '/'){
            //             self.$router.push({path: '/'});
            //         }
            //     }).catch(function(err){
            //         console.log(err);
            //     })
            // }

            // localStorage.removeItem('id');
            // localStorage.removeItem('userName');
            // localStorage.removeItem('identity'); 
            // localStorage.clear();

        },
        //“个人主页”函数
        person(){
            if(localStorage.id?true:false){
                // if(localStorage.identity == 'admin'){
                    // this.$router.push({path: '/audit'});
                    // window.open(window.location.origin+'/secondHandMarket-web/#/audit');                
                // }else{
                    // this.$router.push({path: '/personData',params: this.userData});
                    window.open(window.location.origin+'/secondHandMarket-web/#/personData');                
                // }
            }else{
                this.$Message.info('请先登录~');
            }
        },
        //注册函数
        registered(){
            if(this.register.username!=undefined&&this.register.password!=undefined&&this.register.mobile!=undefined&&this.register.email!=undefined&&this.register.identity!=undefined){
            // this.$refs[name].validate((valid) => {
                // if(valid){
                    let self = this;
                    this.$http(process.env.API_HOST+'user/register',{
                        method: 'post',
                        data: self.register
                    }).then(function(res){
                        if(res.data.status == 200){
                            self.$Modal.info({
                                title: '提示',
                                content: '<h3>注册成功！</h3><p>前往登录~</p>',
                                onOk: () => {
                                    self.modal1 = true;
                                    self.register = {};
                                }
                            })
                        }else{
                            self.$Message.info(res.data.msg);
                        }
                    }).catch(function(err){
                        console.log(err);
                    })
                // }
            // })
            }else{
                this.$Message.info('请将信息填写完整！');
            }
            
        },
        //修改密码函数
        modifyPw(){
            if(this.MPassword1 != '' && this.MPassword2 != ''){
                let self = this;
                this.$http(process.env.API_HOST+'user/modifyPassword',{
                    method: 'post',
                    params: {
                        id: localStorage.id,
                        oldPwd: self.MPassword1,
                        newPwd: self.MPassword2
                    }
                }).then(function(res){
                    if(res.data.status == 200){
                        self.MPassword1 = '';
                        self.MPassword2 = '';
                        self.$Modal.success({
                            title: '提示',
                            content: '<div>修改成功!</div>',
                            onOk: () => {
                                self.exit();
                                self.modal1 = true;
                            }
                        })
                    }else{
                        self.$Message.error('修改失败!'+res.data.msg);
                    }
                }).catch(function(err){
                    console.log(err);
                })
            }else{
                this.$Message.info('请将信息填写完整！');
            }
        },
        PWCancel(){
            this.MPassword1 = '';
            this.MPassword2 = '';
        }
    }
}
</script>
<style lang="less">
    .ivu-modal-body{
        font-size: 18px;
        // padding-left: 110px;
        margin: 40px auto;
        // margin: 40px 0;
        div.login-form{
            margin-top: 15px;
        }
        .ivu-input-wrapper[data-v-7e18a1d6]{
            width: 250px;
            margin-left: 10px;
            .ivu-input{
                font-size: 16px;
                height: 38px;
            }
        }
    }
</style>

<style lang="less" scoped>
// @import 'iview/dist/styles/iview.css';
    .top{
        min-width: 1400px;
        .top_content{
            height: 45px;
            width: 1050px;     
            margin: 0 auto;
            overflow: hidden;
            img{
                margin-top: 9px;
            }
            // box-shadow: 1px #949494;
            dl{
                margin: 0 25px 0 auto;
                line-height: 45px;
                vertical-align: center;
                display: inline-flex;
                float: right;
                color: #504A4D;
                dd{
                    padding: 0 15px;
                    span{
                        cursor: pointer;
                        font-size: 16px;
                        text-align: center;
                    }
                } 
                // dd:hover{
                //     background: #fd5f5c;
                //     color: #fff;
                // }
                dd span:hover{
                    color: #fd5f5c;
                }
            }
        }
        .login-form{
            margin-top: 150px;
        }
        .changecode{
            color: #2255DD;
            cursor: pointer;
            font-size: 14px;
        }
        .changecode:hover{
            text-decoration: underline;

        }
    }
    
</style>




