<template>
    <div class="publishInfo">
    <p class="p_head">当前位置--发布信息--发布供需</p>
    <div class="info">
        <publish :pType='pType' :info='info' :tType="tType"></publish>
    </div>
    </div>
</template>


<style lang="less"scoped>
.publishInfo{
    width: 600px;
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .info{
        margin-top: 20px;
        margin-left: 150px;
    }
}
</style>

<script>
import publish from './publish'
export default {
    components: { publish },
    data: function(){
        return {
            pType: 'publish',
            info: {},
            tType: '1'
        }
    }
    
}
</script>



