<template>
    <div class="top">
        <div class="top_content">
            <img src="../assets/02.png" style="cursor: pointer;float:left;" width="95px">
            <dl>
                <dd v-if="nameonff">{{ usersname }} ,你好 [ <span @click="modal2 = true">修改密码</span> | <span @click="exit">退出</span> ]</dd>    
                <dd v-else><span>未登录</span></dd>
            </dl>
        </div>
        
        <div>
            <Modal v-model="modal2" title="修改密码" @on-ok="modifyPw" @on-cancel="PWCancel">
                <div style="margin-left: 70px;">
                    <label>旧密码</label>
                    <Input type='password' style="width: 250px;" v-model.trim="MPassword1"></Input>
                </div>
                <div style="margin-left: 70px;margin-top: 25px;">
                    <label>新密码</label>
                    <Input type="password" style="width: 250px;" v-model.trim="MPassword2"></Input>
                </div>
            </Modal>
        </div>
    </div>
</template>
<script>
export default {
    props: ['ALState'],
    data(){
        return {
            password: '',
            username: '',
            MPassword1: '',
            MPassword2: '',
            modal1: false,
            modal2: false,
            userData: {},
            usersname: '',
            nameonff: false
        }
    },
    created: function(){
        if(localStorage.userName){
            this.nameonff = true;
            this.usersname = localStorage.userName;            
        }
    },
    watch: {
        'ALState': function(){
            console.log(this.nameOnff+'nlogin');
            this.nameonff = this.ALState;
            if(localStorage.userName)
            this.username = localStorage.userName;
        }
    },
    methods: {
        //图标的回到首页函数
        backHome(){
            this.$router.push({path: '/'});
        },
        //取消登录函数
        cancel(){
            this.$Message.info('已取消登录');
            this.username = '';
            this.password = '';
        },
        //登录管理员请求函数
        login_admin(){
            let self = this;
            this.$http(process.env.API_HOST+'manager/login',{
                method: 'post',
                data:{
                    'username': self.username,
                    'password': self.password
                }
            }).then(function(res){
                if(res.data.status == 200){
                    // self.$store.state.loginState = true;
                    self.nameonff = true;
                    self.$emit('adminLState',true);
                    localStorage.setItem('loginstate',true);
                    let data = res.data.data.data;
                    self.userData = res.data.data.data;
                    delete self.userData.password;
                    delete self.userData.salt;
                    // self.$store.state.loginState = true;
                    localStorage.setItem('id',data.id);
                    localStorage.setItem('userName',data.username);
                    localStorage.setItem('identity','admin');       
                    self.usersname = localStorage.userName; 
                }else{
                    self.$Message.error(res.data);
                }
                self.username = '';
                self.password = '';
            }).catch(function(err){
                self.$Message.error('登录失败！');
            })
        },
        //登录函数
        login(){
            if(this.username != '' && this.password != ''){
                this.login_admin();
            }else{
                this.$Message.info('请将信息填写完整');
            }
            
        },
        //退出函数
        exit(){
            this.userData = {};
            let self = this;
                this.$http(process.env.API_HOST + 'manager/logout',{
                    method: 'delete'
                }).then(function(res){
                    localStorage.clear();
                    self.nameonff = false;                    
                    self.username = '';
                    self.$router.push({path: '/admin'});
                    self.$emit('adminLState',false);
                }).catch(function(err){
                    console.log(err);
                })

        },
        //修改密码函数
        modifyPw(){
            if(this.MPassword1 != '' && this.MPassword2 != ''){
                let self = this;
                this.$http(process.env.API_HOST+'user/modifyPassword',{
                    method: 'post',
                    params: {
                        id: localStorage.id,
                        oldPwd: self.MPassword1,
                        newPwd: self.MPassword2
                    }
                }).then(function(res){
                    if(res.data.status == 200){
                        self.MPassword1 = '';
                        self.MPassword2 = '';
                        self.$Modal.success({
                            title: '提示',
                            content: '<div>修改成功!</div>',
                            onOk: () => {
                                self.exit();
                                self.modal1 = true;
                            }
                        })
                    }else{
                        self.$Message.error('修改失败!'+res.data.msg);
                    }
                }).catch(function(err){
                    console.log(err);
                })
            }else{
                this.$Message.info('请将信息填写完整！');
            }
        },
        //取消修改密码
        PWCancel(){
            this.MPassword1 = '';
            this.MPassword2 = '';
        }
    }
}
</script>
<style lang="less">
    .ivu-modal-body{
        font-size: 18px;
        // padding-left: 110px;
        margin: 40px auto;
        // margin: 40px 0;
        div.login-form{
            margin-top: 15px;
        }
        .ivu-input-wrapper[data-v-7e18a1d6]{
            width: 250px;
            margin-left: 10px;
            .ivu-input{
                font-size: 16px;
                height: 38px;
            }
        }
    }
</style>

<style lang="less" scoped>
// @import 'iview/dist/styles/iview.css';
    .top{
        min-width: 1400px;
        .top_content{
            height: 45px;
            width: 1050px;     
            margin: 0 auto;
            overflow: hidden;
            img{
                margin-top: 9px;
            }
            // box-shadow: 1px #949494;
            dl{
                margin: 0 25px 0 auto;
                line-height: 45px;
                vertical-align: center;
                display: inline-flex;
                float: right;
                color: #504A4D;
                dd{
                    padding: 0 15px;
                    span{
                        cursor: pointer;
                        font-size: 16px;
                        text-align: center;
                    }
                } 
                // dd:hover{
                //     background: #fd5f5c;
                //     color: #fff;
                // }
                dd span:hover{
                    color: #fd5f5c;
                }
            }
        }
        .login-form{
            margin-top: 150px;
        }
        .changecode{
            color: #2255DD;
            cursor: pointer;
            font-size: 14px;
        }
        .changecode:hover{
            text-decoration: underline;

        }
    }
    
</style>




