<template>
    <div class="publish">
        <Form :model="formInfo" :label-width="80">
             <FormItem label="标题">
                 <Input v-model.trim="formInfo.title"></Input>
            </FormItem>
            <FormItem label="交易地点">
                <Input v-model.trim="formInfo.address"></Input>
            </FormItem>
            <FormItem label="类别">
                <RadioGroup v-model.trim="publishType">
                    <Radio label="1">供应</Radio>
                    <Radio label="2">需求</Radio>
                </RadioGroup>
            </FormItem>
            <FormItem label="物品类型">
                <Cascader :data="goodType" v-model.trim="gType"></Cascader>
            </FormItem>
            <FormItem label="价格">
                <Input v-model.trim="formInfo.price"></Input>
            </FormItem>
            <FormItem label="数量">
                <Input v-model.trim="formInfo.num"></Input>
            </FormItem>
            <FormItem label="描述">
                <Input type="textarea" v-model.trim="formInfo.detail"></Input>
            </FormItem>
            <FormItem label="联系人">
                <Input v-model.trim="formInfo.linkPeople"></Input>
            </FormItem>
            <FormItem label="联系方式">
                <Input v-model.trim="formInfo.linkWay"></Input>
            </FormItem>
            <div style="height: 212px;" v-if="publishType==1">
                <FormItem label="图片">                    
                    <Upload :before-upload="handleBeforeUpload" :show-upload-list="false" action="#">
                        <div class="camera" style="display: inline-block">
                            <Icon type="camera" size="20" style=""></Icon>
                        </div>
                        <span>最多只能上传8张图片</span>
                    </Upload>
                    <div class="imgs">
                        <div v-for="item in files" :key="">
                            <img :src="item.url" width="58px" height="58px">   
                            <div @click="deleteImg(item.index)"> 
                                <Icon type="close-circled" style="position: relative;top: -50px;left: -10px;cursor: pointer;"></Icon> 
                            </div>
                        </div>
                    </div>
                </FormItem>
            </div>
            <FormItem>
                <Button type="primary" @click="publish" class="btn" size="large">确认发布</Button>
            </FormItem>
        </Form>
    </div>
</template>
<style lang="less" scoped>
.publish{
    .ivu-upload{
        display: inline-block;
    }
    .ivu-form-item{
        margin-bottom: 14px;
    }
    .imgs{
        div{
            display: inline-block;
        }
    }
    .camera{
        background: rgba(0,0,0,.3);
        width: 58px;
        height:58px;
        line-height: 58px;
        margin-bottom: 15px;
        cursor: pointer;
        i{
            margin-left: 20px;
        }
    }
}
</style>
<script>
export default {
    props:{
        pType: {
            type: String,
            require: true
        },
        info: {
            type: Object,
            require: true
        },
        tType: {
            type: String,
            require: true
        }
    },
    data(){
        return {
            formInfo: {
                title: '',
                address: '',
                type: 1,
                price: '',
                detail: '',
                linkPeople: '',
                linkWay: '',
                userId: 0,
                num: ''
            },
            publishType: '1',
            gType: [],
            goodType: [],
            files: []
        }
    },
    created: function(){
        this.getType(0);
        if(this.info.id){
            // this.formInfo = this.info;
            this.publishType = this.info.tType;
            if(this.info.tType == '2'){
                this.detailWant(this.info.id);
            }else{
                this.detailGood(this.info.id);
            }
        }
    },
    methods: {
        //删除图片的函数
        deleteImg(index){
            this.files.splice(index,1);
            for(let i=0;i<this.files.length;i++){
                this.files[i].index = i;
            }
        },
        //获取需求的详情的函数
        detailWant(id){
            let self = this;
            this.$http(process.env.API_HOST+'publish/want/get/'+id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 200){
                    self.gType = [];
                    self.formInfo = res.data.data;
                    self.formInfo.tType = '2';
                    self.publishType = self.tType;
                    self.gType.push(self.formInfo.parentTypeId);
                    self.gType.push(self.formInfo.typeId);
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //获取商品的详情
        detailGood(id){
            let self = this;
            this.$http(process.env.API_HOST+'publish/goods/get/'+id,{
                method: 'get',
                params:{
                    status: 0
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.gType = [];
                    self.files = [];
                    // self.formInfo = 
                    let data = res.data.data;
                    self.formInfo = {
                        'id': data.id,
                        'address': data.address,
                        'linkPeople': data.linkPeople,
                        'linkWay': data.linkWay,
                        'title': data.title,
                        'detail': data.detail,
                        'price': data.price
                    };
                    self.publishType = self.tType;
                    self.formInfo.tType = '1';
                    self.gType.push(data.parentTypeId);
                    self.gType.push(data.typeId);
                    for(let i=0;i<data.pic.length;i++){
                        self.files.push({'url': data.pic[i],'index': i});
                    }
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //获取所有的类型
        getType(value){
            let self = this;
            this.$http(process.env.API_HOST+'manager/type/get/'+value,{
                method: 'get'
            }).then(function(res){
                if(res.status == 200){
                    self.goodType = [];
                    let data = res.data.data;
                    data.forEach(e1 => {
                        let c = [];
                        if(e1.childList){
                            e1.childList.forEach(e2 => {
                                c.push({
                                    'value': e2.id,
                                    'label': e2.name 
                                });
                            });
                        };
                        let d  = {
                            'value': e1.id,
                            'label': e1.name,
                            'children': c
                        };
                        self.goodType.push(d);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //“确认发布”的函数
        publish(){
            if(this.formInfo.title!=''&&this.formInfo.address!=''&&this.formInfo.price!=''&&this.formInfo.detail!=''&&this.linkPeople!=''&&this.linkWay!=''){
                let self = this;
                if(this.pType == 'modify'){
                    if(self.formInfo.type == 1 || self.formInfo.tType == '1'){
                        self.changeGood();
                    }else{
                        self.changeWant();
                    }
                }else{
                    self.formInfo.typeId = self.gType[1];
                    // if(self.formInfo.type == 1){
                    if(self.publishType == 1){
                        self.publish_goods();
                    }else{
                        self.publish_want();
                    }
                }
            }else{
                this.$Message.info('请将信息填写完整！');
            }
            
        },
        //发布商品
        publish_goods(){
            this.formInfo.userId = localStorage.id;
            var form = new FormData();
            let formInfo = this.formInfo;
            for(var key in formInfo){
                form.append(key,formInfo[key]);
            };
            this.files.forEach(e1 => {
                form.append('pubishPic',e1);
            });

            let self = this;
            this.$http(process.env.API_HOST+'publish/goods/post',{
                method: 'post',
                data: form,
                headers: {
                    "Content-Type": "application/x-www-form-urlencoded"
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.formInfo = {};
                    self.files = [];
                    self.gType = [];
                    self.$Message.info('成功提交申请！');
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }else{
                    self.$Message.error('提交申请失败！'+res.data);
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //发布需求
        publish_want(){
            // this.formInfo['detailImg'] = this.files;
            // let info = new FormData();
            this.formInfo.userId = localStorage.id;
            let self = this;
            this.$http(process.env.API_HOST+'publish/want/post',{
                method: 'post',
                data: self.formInfo
            }).then(function(res){
                if(res.data.status == 200){
                    self.formInfo = {};
                    self.$Message.info('成功提交申请！');
                    self.files = [];
                    self.gType = [];
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }else{
                    self.$Message.error('提交申请失败！');
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //文件上传前调用的函数
        handleBeforeUpload (file) {
            if(this.files.length < 8){
                let self =this;
                let reader = new FileReader();
                reader.readAsDataURL(file);
                const _this = this;
                reader.onloadend = function(e){
                    file.url = reader.result;
                    file.index = self.files.length;
                    self.files.push(file);
                }
            }else{
                this.$Message.info('最多只能上传8张图片');
            }
            return false;
        },
        //修改需求的函数
        changeWant(){
            let self = this;
            this.$http(process.env.API_HOST+' publish/want/put',{
                method: 'put',
                data: self.formInfo
            }).then(function(res){
                if(res.data.status == 200){
                    self.$Modal.success({
                        title: '提示',
                        content: '修改成功！',
                        onOk: ()=>{
                            self.formInfo = {};
                            self.$emit('modifySuccess');
                        }
                    });
                    self.formInfo = {};
                    self.gType = [];
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }else{
                    self.$Message.info(res.data);
                }
            }).catch(function(err){
                    self.$Message.info('操作失败！');
                    console.log(err);
            })
        },
        //修改商品的函数
        changeGood(){
            let self = this;
            let file = [];
            let file2 = [];
            let files = this.files;
            for(let i=0;i<files.length;i++){
                if(files[i].name){
                    file.push(files[i]);
                    // self.files.splice(i,1);
                }else{
                    file2.push(files[i].url);
                }
            }
            var form = new FormData();
            let formInfo = this.formInfo;
            for(var key in formInfo){
                if(key != 'tType')
                form.append(key,formInfo[key]);
            };
            form.append('oriPic',file2);
            file.forEach(e1 => {
                form.append('pubishPic',e1);
            });
            form.append('typeId',this.gType[1]);
            form.append('userId',localStorage.id);
            delete this.formInfo.tType;
            this.$http(process.env.API_HOST+'publish/goods/put',{
                method: 'post',
                data: form,
                headers: {
                    'Content-Type': "application/x-www-form-urlencoded"
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.gType = [];
                    self.files = [];
                    self.$Modal.success({
                        title: '提示',
                        content: '修改成功！',
                        onOk: ()=>{
                            self.formInfo = {};
                            self.$emit('modifySuccess');
                        }
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }else{
                    self.$Message.info(res.data);
                }
            }).catch(function(err){
                    self.$Message.error('操作失败！');
                    console.log(err);
            })
        }
    },
    //销毁实例的函数
    deactivated(){
        this.$destroy(true);
    }
}
</script>




