                align: 'center',
                width: 120
            },{
                title: '操作',
                key: 'action',
                width: 170,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            on: {
                                click: ()=>{
                                    // this.remove(params.index,'sell');
                                    this.onff = false;
                                    this.detailInfo = params.row;
                                    this.detailInfo.index = params.index;
                                    this.detailInfo.way = 'sell';
