        //             })
        //         }else{
        //             console.log(err);
        //         }
        //     })
        // },
        //获取订单列表
        orderList_sell(page){
            let self = this;
            this.$http(process.env.API_HOST+'orders/operateListByUserId',{
                method: 'get',
                params: {
                    'page': page,
                    'rows': 10
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.orderData = [];
                    let data = res.data.data.rows;  
                    self.pages1 = res.data.data.total;
                    data.forEach(e1 => {
                            let time = e1.creatTime.split(' ');
                            // let d = {
                            //     'title': e1.title,
                            //     'creatTime': time[0],
                            //     'id': e1.id,
                            //     'userName': e1.userName,
                            //     'goodsNum': e1.goodsNum,
                            //     'goodsPrice': e1.goodsPrice,
                            //     'totalPrice': e1.goodsNum*e1.goodsPrice
                            // };
                            let d = e1;
                            d.creatTime = time[0];
                            self.orderData.push(d);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
