            //     id = this.orderData2[index].id;
            // }else{
            //     id = this.orderData1[index].id;
            // }
            let index = this.detailInfo.index;
            let type = this.detailInfo.way;
            let id = this.orderData[index].id;
            // console.log(id);
            let self = this;
            this.$http(process.env.API_HOST+'orders/cancel/'+id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 200){
                    self.$Modal.success({
                            title: '提示',
                            content: '<p>删除成功！</p>',
                            onOk: ()=>{
                                self.back();
                                self.orderData.splice(index,1);
                            }
                        });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }else{
                    self.$Message.info(res.data.msg);
                }
            }).catch(function(err){
                console.log(err);
            });
        },
