<style lang="less" scoped>
.deleteOrder{
    width: 100%;
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .ivu-table-wrapper{
        height: 616px;
    }
    .ivu-page{
        margin-top: 20px;
    }
    .reportGood{
            overflow: hidden;
            margin-bottom: 15px;
            .message{
                display: inline-block;
                width: 500px;
                word-wrap: break-word;
                word-break: normal;
            }
        label{
            font-weight: bold;
            display: inline-block;
            width: 80px;
            float: left;
        }
        dl{
            display: inline-block;
            dd{
                float: left;
                margin-right: 15px;
            }
        }
    }
    .back_btn{
        color: #f5866e;
        font-size: 16px;
        cursor: pointer;
        position: relative;
        top: -35px;
        left: 650px;
    }
    .back_btn:hover{
