<template>
    <div class="deletePublish">
        <p class="p_head">当前位置--发布信息--删除信息</p>
        <Select v-model="tType2" @on-change="getInfo">
            <Option v-for="item in tabelList" :key="item.value" :value="item.value">{{ item.label }}</Option>
        </Select>
        <Table height="616" :columns="columns_info" :data="data_info"></Table>
        <Page :total="pages" :page-size="12" @click="changePage"></Page>
    </div>
</template>
<script>
export default {
    data(){
        return {
            status: '',
            pages: 100,
            tType: '1',
            tType2: '3',
            tabelList: [{
                value: '2',
                label: '需求'
            },{
                value: '3',
                label: '全部商品'
            },{
                value: '4',
                label: '正在审核的商品'
            },{
                value: '5',
                label: '审核通过的商品'
            },{
                value: '6',
                label: '审核不通过的商品'
            }],
            columns_info: [{
                title: '标题',
                key: 'title',
                align: 'center',
                width: 430
            },{
                title: '日期',
                align: 'center',
                key: 'time',
                width: 120
            },{
                title: '类型',
                align: 'center',
                key: 'type',
                width: 110
            },{
                title: '操作',
                align: 'center',
                key: 'action',
                width: 100,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'error',
                                size: 'small'
                            },
                            on: {
                                click: ()=>{
                                    this.deleteInfo(params.index)
                                }
                            }
                        },'删除')
                    ]);
                }
            }],
            data_info: []
        }
    },
    created: function(){
        this.getInfo();
    },
    methods: {
        //删除商品
        deleteGood(index){
            let self = this;
            let id = this.data_info[index].id;
            this.$http(process.env.API_HOST + 'publish/goods/delete/'+id,{
                method: 'delete'
            }).then(function(res){
                if(res.data.status  == 200){
                    self.data_info.splice(index,1);
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //删除需求
        deleteWant(index){
            let id = this.data_info[index].id;
            let self = this;
            this.$http(process.env.API_HOST+'publish/want/delete/'+id,{
                method: 'delete'
            }).then(function(res){
                if(res.data.status == 200){
                    self.data_info.splice(index,1);
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        // getInfo(page){
        //     let self = this;
        //     this.$http(process.env.API_HOST+'publish/list',{
        //         method: 'get',
        //         params: {
        //             'status': self.tType,
        //             'page': page,
        //             'row': 15,
        //             'typeId': 0
        //         }
        //     }).then(function(res){
        //         if(res.data.status == 200){
        //             let data = res.data.data.rows;
        //             self.pages = res.data.data.total;
        //             data.forEach(e1 => {
        //                 let time =  e1.time.split(' ');
        //                 let d = {
        //                     'address': e1.address,
        //                     'title': e1.title,
        //                     'time': time[0],
        //                     'id': e1.id
        //                 };
        //                 self.data_info.push(d);
        //             });
        //         }
        //     }).catch(function(err){
        //         if(err.data.status == 410){
        //             self.$Modal.info({
        //                 title:  '提示',
        //                 content: '<p>登录信息失效，请重新登录！'
        //             })
        //         }else{
        //             console.log(err);
        //         }
        //     })
        // },
        //页码改变
        changePage(page){
            if(this.tType == '2'){
                this.getPublish_want(page);
            }else{
                switch(this.tType2){
                    case '3': this.getPublish_good(page,0);break;//全部
                    case '4': this.getPublish_good(page,1);break;//正在审核
                    case '5': this.getPublish_good(page,2);break;//审核通过
                    default: this.getPublish_good(page,3);//审核不通过
                }
            }
        },
        //获取信息
        getInfo(value){
            if(value == '2'){
                this.tType = '2';
                this.getPublish_want(1);
            }else{
                this.tType = '1';
                    console.log(value);
                switch(value){
                    case '3': this.getPublish_good(1,0);break;//全部
                    case '4': this.getPublish_good(1,1);break;//正在审核
                    case '5': this.getPublish_good(1,2);break;//审核通过
                    default: this.getPublish_good(1,3);//审核不通过
                }                
            }
        },
        //获取商品列表
        getPublish_good(page,status){
            let self = this;
            this.$http(process.env.API_HOST+'publish/goods/list/'+localStorage.id,{
                method: 'get',
                params: {
                    page: page,
                    row: 12,
                    status: status
                }
            }).then(function(res){
                 if(res.data.status == 200){
                     self.data_info = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;
                    data.forEach(e1 => {
                        let time  = e1.time.split(' ');
                        let d = {
                            'title': e1.title,
                            'time': time[0],
                            'type': e1.type,
                            'id': e1.id
                        };
                        self.data_info.push(d);
                    });
                 }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //获取需求列表
        getPublish_want(page){
            let self = this;
            this.$http(process.env.API_HOST+'publish/want/list/'+localStorage.id,{
                method: 'get',
                params: {
                    page: page,
                    row: 12
                }
            }).then(function(res){
                if(res.data.status == 200){
                     self.data_info = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;
                    data.forEach(e1 => {
                        let time  = e1.time.split(' ');
                        let d = {
                            'title': e1.title,
                            'time': time[0],
                            'type': e1.type,
                            'id': e1.id
                        };
                        self.data_info.push(d);
                    });
                    
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //处理‘删除’
        deleteInfo(index){
            if(this.tType == 1){
                this.deleteGood(index);
            }else{
                this.deleteWant(index);
            }
        }
    }
}
</script>

<style lang="less" scoped>
.deletePublish{
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .ivu-table-wrapper{
        height: 616px;
    }
    .ivu-page{
        margin-top: 20px;
    }
}
</style>



