<template>
    <div class="detailInfo">
        <img class="img" src="../assets/05.jpg" width="100%">
        <div class="detail">
            <div>
                <img :src="detailData.picture" width="450px" height="350px">
                <div class="info">
                    <h4>{{ detailData.title }}</h4>
                    <div class="time"><span>发布时间：{{ detailData.time }}</span> <span style="margin-left: 30px">浏览次数：{{ detailData.viewCount }}</span></div>
                    <div class="price">￥{{ detailData.price }}</div><div class="num">库存量: {{ detailData.num }}</div>
                    <p>{{ detailData.detail }}</p>
                    <div class="address">交易地点：{{ detailData.address }}</div>
                    <div class="datadiv">联系人：{{ detailData.linkPeople }}</div>
                    <!-- <div class="datadiv">联系方式：{{ detailData.linkWay }}</div> -->
                    <div>
                        <Button class="orderBtn" type="primary" @click="initiateOrder">发起订单</Button>
                        <Button class="orderBtn" v-if="collectOnff" @click="collect">收藏</Button>
                        <Button type="primary" v-else @click="collectCancel">已收藏</Button>
                    </div>
                </div>
            </div>

            <div class="imgs">
                <h3>商品图片</h3>
                <dl>
                    <dd v-for="item in detailData.pic" :key=""><img :src="item" width="350px" height="350px"></dd>                    
                </dl>
            </div>

        </div>

        <div>
            <Modal v-model.trim="modal1" title="新建订单" @on-ok="newOrder" @on-cancel="cancel">
                <div>
                    <label style="display: inline-block;width: 80px;margin-left: 30px;">联系人</label>
                    <Input style="width: 350px" v-model.trim="linkPeople" placeholder="请输入联系人"></Input>
                </div>
                <div>
                    <label style="display: inline-block;width: 80px;margin: 20px 0 20px 30px;">联系方式</label>
                    <Input style="width: 350px" v-model.trim="linkWay" placeholder="请输入联系方式"></Input>
                </div>
                <div>
                    <label style="display: inline-block;width: 80px;margin: 0 0 0 30px;">数量</label>
                    <InputNumber :min="1" :max="detailData.num" v-model="number"></InputNumber>
                </div>
                <div>
                    <label style="display: inline-block;width: 80px;margin: 20px 0 20px 30px;">总价</label>
                    <span>{{ number * detailData.price }} 元</span>
                </div>
                <div>
                    <label style="display: inline-block;width: 80px;margin-left: 30px;">备注：</label>
                    <Input style="width: 350px" type="textarea" v-model.trim="message" placeholder="请输入备注"></Input>
                </div>
            </Modal>
        </div>
        

    </div>
</template>
<script>
export default {
    data(){
        return{
            detailData: {},
            modal1: false,
            message: '',
            linkPeople: "",
            linkWay: '',
            ptype: '',
            collectOnff: true,
            number: 1
        }
    },
    created: function(){
        let id = this.$route.query.id;
        this.getGood(id);
    },
    methods: {
        //获取商品详情
        getGood(id){
            let self = this;
            this.$http(process.env.API_HOST+'publish/goods/get/'+id,{
                method: 'get',
                params: {
                    status: 1
                }
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data;       
                    let time = data.time.split(' ');
                    self.detailData = {
                        'address': data.address,
                        'detail': data.detail,
                        'id': data.id,
                        'linkPeople': data.linkPeople,
                        'linkWay': data.linkWay,
                        'pic': data.pic,
                        'picture': data.picture,
                        'price': data.price,
                        'time': time[0],
                        'title': data.title,
                        'userId': data.userId,
                        'viewCount': data.viewCount,
                        'num': data.num
                    };
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //下订单函数
        newOrder(){
            if(this.message != '' && this.linkPeople != '' && this.linkWay != ''){
                let self = this;
                this.$http(process.env.API_HOST+'orders/place',{
                    method: 'post',
                    data: {
                        'goodsId': self.detailData.id,
                        'userId': localStorage.id,
                        'message': self.message,
                        'userName': self.linkPeople,
                        'linkPhone': self.linkWay,
                        'userId': localStorage.id,
                        'sellerId': self.detailData.userId,
                        'goodsPrice': self.detailData.price,
                        'goodsTotal': self.detailData.price*self.number,
                        'goodsNum': self.number
                    }
                }).then(function(res){
                    if(res.data.status == 200){
                        self.$Message.info('下单成功！');
                        self.message = '';
                        self.linkWay = '';
                        self.linkPeople = '';
                        self.number = 1;
                    }else if(res.data.status == 410){
                        self.$Modal.info({
                            title:  '提示',
                            content: '<p>登录信息失效，请重新登录！'
                        });
                    }else{
                        self.$Message.error(res);
                    }
                }).catch(function(err){
                        console.log(err);
                })
            }else{
                this.$Message.info('请将信息填写完整!');
            }
            
        },
        //下单函数
        initiateOrder(){
            if(localStorage.id){
                this.modal1 = true;
                this.message = '';
                this.linkPeople = '';
                this.linkWay = '';
            }else{
                this.$Message.info('请先登录~');
            }
        },
        //取消下单函数
        cancel(){
            this.$Message.info('取消下单！');
        },
        //收藏函数
        collect(){
            if(localStorage.id){
                let self = this;
                this.$http(process.env.API_HOST+'collector/'+localStorage.id+'/add/'+this.detailData.id,{
                    method: 'get'
                }).then(function(res){
                    self.$Message.info('收藏成功！');
                    self.collectCancel = false;
                }).catch(function(err){
                    self.$Message.error('收藏失败！');
                })
            }else{
                this.$Message.info('请先登录~');
            }
            
        },
        //取消收藏函数
        collectCancel(){
            let self = this;
            this.$http(process.env.API_HOST+'collector/delete/'+self.detailData.id,{
                method: 'get'
            }).then(function(res){
                self.collectOnff = true;
                self.$Message.info('已取消收藏！');
            }).catch(function(err){
                self.$Message.error('取消收藏失败！');
            })
        }
    }
    
}
</script>

<style lang="less" scoped>
    .detailInfo{
        .img{
            min-width: 1400px;
        }
        .detail{
            width: 1050px;
            margin: 0 auto;
            div:first-child{
                margin-top: 30px;
                overflow: hidden;
                img{
                    float: left;
                }
                .info{
                    // display: inline-block;
                    margin-left: 30px;
                    float: left;
                     h4{
                        display: inline-block;
                        font-size: 24px;
                        margin-top: 5px;
                    }
                    .price{
                        color: #ff5230;
                        font-size: 22px;
                        margin: 10px 0 10px;
                        display: inline-block;
                        width: 180px;
                    }
                    .num{
                        display: inline-block;
                        
                    }
                    p{
                        max-width: 680px;
                        font-size: 18px;
                    }
                    .time{
                        color: #848ea5;
                        font-size: 14px;
                        margin-top: 5px;
                    }
                    .datadiv{
                        display: inline-block;
                        font-size: 14px;
                        color: #70788a;
                        margin-right: 25px;
                        margin-top: 5px;
                    }
                    .address{
                        font-size: 16px;
                        margin-top: 8px;
                    }
                    .contact{
                        font-size: 16px;
                        margin-top: 20px;
                    }
                    .orderBtn{
                        margin-top: 20px;
                    }
                }
               
            }
            .imgs{
                margin-top: 80px;
                h3{
                    font-size: 20px;
                }
                dl{
                    dd{
                        display: inline-block;
                        margin-top: 20px;
                        padding: 20px;
                        box-shadow: 0 2px 8px 0 rgba(200, 200, 206, 0.5);
                    }
                    dd:nth-of-type(even){
                        // margin: 0 50px;
                        margin-left: 60px;
                    }
                    dd:nth-of-type(odd){
                        margin-left: 100px;
                    }
                }
            }
        }
        
    }
</style>



