<template>
    <div class="report">
        <p class="p_head">当前位置--审核举报</p>
        <div v-if="onff">
            <Table height="665px" :columns="table_columns" :data="table_data"></Table>
            <Page :page-size="13" :total="pages" @on-change="pageChange"></Page>
        </div>
        <div v-else>
            <div class="reportDetail">
                <label>原因</label>
                <span>{{ detailData.reason }}</span>
            </div>
            <div class="reportDetail">
                <label>日期</label>
                <span>{{ detailData.time }}</span>
            </div>
            <div class="reportDetail">
                <label>图片</label>
                <dl>
                    <dd v-for="item in detailData.picArray" :key="">
                        <img :src="item" width="150px" height="150px">
                    </dd>
                </dl>
            </div>
            <div class="reportDetail">
                <label>审核意见</label>
                <Input type="textarea" v-model.trim="checkResult" style="width: 350px;"></Input>
            </div>
            <div class="reportDetail_btn">
                <Button type="primary" @click="checkReport(2)">同意</Button>
                <Button type="primary" @click="checkReport(3)">不同意</Button>  
            </div>
        </div>

    </div>
</template>
<script>
export default {
    data(){
        return {
            table_columns: [{
                title: '举报原因',
                key: 'reason',
                width: 460
            },{
                title: '日期',
                key: 'time',
                width: 180
            },{
                title: '操作',
                key: 'action',
                width: 120,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            on: {
                                click: ()=>{
                                    this.onff = false;
                                    this.detailData = params.row;
                                }
                            }
                        },'查看详情')
                    ]);
                }
            }],
            table_data: [],
            pages: 0,
            onff: true,
            detailData : {},
            checkResult: ''
        }
    },
    created: function(){
        this.getReportList(1);
    },
    watch: {
        '$route'(to,from){
            if(to.name == 'report'){
                this.onff = true;
                this.getReportList(1);
            }
        }
    },
    methods: {
        //审核举报
        checkReport(value){
            if(this.checkResult != ''){
                let self = this;
                this.$http(process.env.API_HOST+'report/checkReport',{
                    method: 'put',
                    data: {
                        'id': self.detailData.id,
                        'checkStatus': value,
                        'checkResult': self.checkResult
                    }
                }).then(function(res){
                    if(res.data.status == 200){
                        self.$Modal.success({
                            title: '提示',
                            content:'<p>审核举报成功</p>',
                            onOk: () => {
                                self.onff = true;
                                self.detailData = {};
                                self.checkResult = '';
                                self.getReportList(1);
                            }
                        })
                    }else if(res.data.status == 410){
                        self.$Modal.info({
                            title:  '提示',
                            content: '<p>登录信息失效，请重新登录！'
                        });
                    }else{
                        self.$Message.info(res.data.msg);
                    }
                }).catch(function(err){
                    console.log(err);
                    self.$Message.info('审核有误！');
                })
            }else{
                this.$Message.info('请输入审核意见！');
            }
        },
        //获取商品举报列表
        getReportList(page){
            let self = this;
            this.$http(process.env.API_HOST+'report/getReportList',{
                method: 'get',
                params: {
                    'page': page,
                    'row': 10
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.table_data = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;
                    data.forEach(e1 => {
                        let time = e1.reportTime.split(' ');
                        let reason = e1.reason;
                        if(reason.length > 15)
                        reason = reason.substring(0,15) + '...';
                        let d = {
                            'reason': reason,
                            'time': time[0],
                            'id': e1.id,
                            'picArray': e1.picArray
                        };
                        self.table_data.push(d);
                    });
                }
            })
        },
        //页码改变函数
        pageChange(page){
            this.getReportList(page)
        },
        //返回函数
        back(){
            this.onff = true;
            this.checkResult = '';
        }
    }
}
</script>
<style lang="less" scoped>
.report{
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .reportDetail{
        overflow: hidden;
        margin-top: 15px;
        label{
            display: inline-block;
            width: 80px;
            font-weight: bold;
            float: left;
        }
        dl{
            float: left;
            // display: inline-block;      
            height: 300px;      
            dd{
                display: inline-block;
                margin-right: 15px;
            }
        }

    }
    .back_btn{
        color: #f5866e;
        font-size: 16px;
        cursor: pointer;
        position: relative;
        top: -35px;
        left: 650px;
    }
    .back_btn:hover{
        text-decoration: underline;
    }
    .reportDetail_btn{
        margin-left: 80px;
        margin-top: 50px;
    }
}
</style>



