<template>
    <div class="modifyPublish">
        <p class="p_head">当前位置--发布信息--修改信息</p>

        <div v-if="onff">
            <Select v-model="tType2" @on-change="handleTabel">
                <Option v-for="item in tableList" :key="item.value" :value="item.value">{{ item.label }}</Option>
            </Select>
            <Table height='618' :columns="columns_info" :data="data_info"></Table>
            <Page :page-size="12" :total="pages" @click="pageChange"></Page>
        </div>
        
        <div v-else class="modifyInfo">
            <span class="reback" @click="reback">返回</span>
            <publish :info = "info" :tType='tType' :pType = 'pType' @modifySuccess="modifySuccess"></publish>
        </div>
        
    </div>
</template>
<script>
import publish from './publish'
export default {
    components: { publish },
    data(){
        return {
            info: {},
            pType: 'modify',
            tType: '1',
            tType2: '3',
            tableList: [{
                value: '2',
                label: '需求'
            },{
                value: '3',
                label: '全部商品'
            },{
                value: '4',
                label: '正在审核的商品'
            },{
                value: '5',
                label: '审核通过的商品'
            },{
                value: '6',
                label: '审核不通过的商品'
            }],
            onff: true,
            formInfo: {
                title: '',
                address: '',
                publishType: '1',
                type: 1,
                price: '',
                detail: '',
                linkPeople: '',
                linkWay: '',
                // sum: ''
                detailImg: []
            },
            pages: 100,
            columns_info: [{
                title: '标题',
                key: 'title',
                align: 'center',
                width: 420
            },{
                title: '日期',
                align: 'center',
                key: 'time',
                width: 120
            },{
                title: '联系人',
                align: 'center',
                key: 'linkPeople',
                width: 120
            },{
                title: '操作',
                align: 'center',
                key: 'action',
                width: 100,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            on: {
                                click: ()=>{
                                    this.modifyInfo(params.index)
                                }
                            }
                        },'修改')
                    ]);
                }
            }],
            data_info: [], 
        }
    },
    created: function(){
        this.getPublish_good(1,12,0);
    },
    watch: {
        '$route'(to,form){
            if(to.name == 'modifyPublish'){
                this.reback();
                this.getPublish_good(1,12,0);                
            }
        }
    },
    methods: {
        //修改信息
        modifyInfo(index){
            this.onff = false;
            this.info = this.data_info[index];
            this.info['tType'] = this.tType;
        },
        //修改成功
        modifySuccess: function(){
            this.onff = true;
            this.getPublish_good(1,12,0);
        },
        //返回函数
        reback(){
            this.onff = true;
        },
        //获取商品信息列表
        getPublish_good(page,row,status){
            console.log('1dafd');
            let self = this;
            this.$http(process.env.API_HOST+'publish/goods/list/'+localStorage.id,{
                method: 'get',
                params: {
                    page: page,
                    row: row,
                    status: status
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.data_info = [];
                    self.pages = res.data.data.total;
                    let data = res.data.data.rows;
                    data.forEach(e1 => {
                        let time = e1.time.split(' ');
                        let d = {
                            'address': e1.address,
                            'detail': e1.detail,
                            'id': e1.id,
                            'linkPeople': e1.linkPeople,
                            'picture': e1.picture,
                            'time': time[0],
                            'title': e1.title
                        };
                        self.data_info.push(d);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            });
        },
        //获取需求信息列表
        getPublish_want(page,row){
            let self = this;
            this.$http(process.env.API_HOST+'publish/want/list/'+localStorage.id,{
                method: 'get',
                params: {
                    'page':page,
                    'row': row
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.data_info = [];
                    self.pages = res.data.data.total;
                    let data = res.data.data.rows;
                    data.forEach(e1 => {
                        let time = e1.time.split(' ');
                        let d = {
                            'address': e1.address,
                            'detail': e1.detail,
                            'id': e1.id,
                            'linkPeople': e1.linkPeople,
                            'picture': e1.picture,
                            'time': time[0],
                            'title': e1.title
                        };
                        self.data_info.push(d);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //页码改变
        pageChange(page){
            let self = this;
            if(this.tType == '2'){
                this.getPublish_want(page,12);
            }else{
                switch(this.tType2){
                    case '3': self.getPublish_good(1,12,0);break;//全部
                    case '4': self.getPublish_good(1,12,1);break;//正在审核
                    case '5': screenLeft.getPublish_good(1,12,2);break;//审核通过
                    default: self.getPublish_good(1,12,3);//审核不通过
                }
            }
        },
        //选择类型后处理的函数
        handleTabel(value){
            if(value == '2'){
                this.tType = '2';
                this.getPublish_want(1,12);
            }else{
                this.tType = '1';
                switch(value){
                    case '3': this.getPublish_good(1,12,0);break;//全部
                    case '4': this.getPublish_good(1,12,1);break;//正在审核
                    case '5': this.getPublish_good(1,12,2);break;//审核通过
                    default: this.getPublish_good(1,12,3);//审核不通过
                }                
            }
        }
    }
}
</script>

<style lang="less" scoped>
.modifyPublish{
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .modifyInfo{
        width: 400px;
        margin-left: 170px;
        .reback{
            position: relative;
            top: -32px;
            left: 400px;
            color: #f5866e;
            cursor: pointer;
        }
        .reback:hover{
            text-decoration: underline;
        }
    }
    .ivu-table-wrapper{
        height: 618px;
    }
    .ivu-page{
        margin-top: 10px;
    }
}
</style>



