<template>
    <div class="cancelPunish">
        <p class="p_head">当前位置--解除禁止</p>
        <div style="width: 860px">
            <Table height='615' style="margin-right: 100px" :columns="userList_columns" :data="userList"></Table>
            <Page :total="pages" :page-size="10" @on-change="pageChange"></Page>
        </div>
        
    </div>
</template>
<script>
export default {
    data(){
        return {
            userList_columns: [{
                title: '用户名',
                key: 'username',
                width: 150
            },{
                title: '联系方式',
                key: 'mobile',
                width: 200
            },{
                title: '邮箱',
                key: 'email',
                width: 250
            },{
                title: '操作',
                key: 'action',
                width: 150,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            on: {
                                click: ()=>{
                                    this.delpunish(params.row,params.index);
                                    this.onff = false;
                                    this.detailInfo = params.row;
                                }
                            }
                        },'解除禁止')
                    ]);
                }
            }],
            pages: 0,
            userList: []
        }
    },
    created: function(){
        this.getPunishList(1);
    },
    methods: {
        //解除用户惩罚
        delpunish(row,index){
            let self = this;
            this.$http(process.env.API_HOST+'manager/delpunish/'+row.id,{
                method: 'delete',
            }).then(function(res){
                if(res.data.status == 200){
                    self.$Message.info('解除禁止成功！');
                    self.userList.splice(index,1);
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //获取举报列表
        getPunishList(page){
            let self = this;
            this.$http(process.env.API_HOST+'manager/user/listReported',{
                method: 'get',
                params: {
                    'page': page,
                    'rows': 10
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.pages = res.data.data.total;
                    let data =  res.data.data.rows;
                    self.userList = data;
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //页码改变
        pageChange(page){
            this.getUserList(page);
        },
    }
}
</script>
<style lang="less" scoped>
.cancelPunish{
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .reportGood{
            overflow: hidden;
            margin-bottom: 15px;
        label{
            font-weight: bold;
            display: inline-block;
            width: 80px;
            float: left;
        }
        dl{
            display: inline-block;
            dd{
                float: left;
                margin-right: 15px;
            }
        }
    }
}
</style>



