<template>
    <div class="browseInfo">
        <img class="img" src="../assets/05.jpg" width="100%">
        <!-- <div>
            <Carousel autoplay v-model="value" loop>
                <CarouselItem v-for="item in shufflingData" :key="">
                    <div>
                        <div class="shuffling">
                            <img class="img" :src="item.picture">
                            <h2 class="title">{{ item.title }}</h2>
                            <span class="detail">{{ item.detail }}</span>
                            <span>联系人: {{ item.linkPeople }}</span>
                            <span>联系方式: {{ item.linkWay }}</span>
                        </div>                
                    </div>
                </CarouselItem>
            </Carousel>
        </div> -->
        <!-- <img class="img" src="../assets/03.jpg" width="100%"> -->
        <!-- <div class="search">
            <input type="text" v-model.trim="searchWord">
            <button @click="search">搜 索</button>
            <RadioGroup class="search_radio" v-model="status">
                <Radio label="1"><span>供应</span></Radio>
                <Radio label="2"><span>需求</span></Radio>
            </RadioGroup>
        </div> -->

        <div class="nav">
            <div>
                <Select v-model="way" style="width: 200px;margin-top: 50px;" @on-change="selectWay">
                    <Option v-for="item in typeList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
                </Select>
            </div>
            <div v-if="way=='type'">
                <RadioGroup v-model="stype">
                    <Radio label="1">供应</Radio>
                    <Radio label="2">需求</Radio>
                </RadioGroup>
            </div>
            <div v-if="way=='type'" v-for="item in typeData" :key="">
                <div class="type_title">{{ item.name }}</div>
                <span class="type_child" :class="{type_child_choose:item2.id==choose}" v-for="item2 in item.childList" @click="search_typerequest(item2.id)" :key="">{{ item2.name }}</span>
            </div>
            <!-- <div v-for="item in typeData" :key="">
                <Dropdown class="dropdown">
                    <a href="javascript:void(0)">
                        {{ item.name }}
                         <Icon type="arrow-down-b"></Icon> 
                    </a>
                    <DropdownMenu slot="list">
                        <DropdownItem  v-for="item2 in item.childList" :key="">{{ item2.name }}</DropdownItem>
                    </DropdownMenu>
                </Dropdown>
            </div> -->
            <!-- <div>
                <Menu mode="horizontal" theme="light" active-name="1" @on-select="search_typerequest">
                     <MenuItem :name="item.id" v-for="item in typeData" :key="">
                        {{ item.name }}
                    </MenuItem> 
                    <Submenu :name="item.id" v-for="item in typeData" :key="item.id">
                        <template slot="title">
                            {{ item.name }}
                        </template>
                        <MenuGroup>
                            <MenuItem :name="item2.id" v-for="item2 in item.childList" :key="item2.id">{{ item2.name }}</MenuItem>
                        </MenuGroup>
                    </Submenu>
                </Menu>
            </div> -->
        </div>

        <div class="info">
            <div>
                <dl>
                    <dd v-if="onff == 1" class="browseInfo_dd" v-for="item in info" :key="">
                        <Card>
                            <div class="info_div">
                                <img :src="item.picture" width="155px" height="155px">
                                <h3 @click="detail_good(item.id)">{{ item.title }}</h3>
                                <div>
                                    <span class="price">￥{{ item.price }}</span>
                                    <span class="count">浏览数：{{ item.viewCount }}</span>
                                </div>
                                <span class="time">{{ item.time }}</span>
                                <span class="name">{{ item.linkPeople }}</span>
                            </div>
                        </Card>
                    </dd>
                    <dd  class="browseInfo_dd browseInfo_dd2" v-else>
                        <Card>
                            <div class="info_div">                                
                                <h3 @click="detail_want(item.id)">{{ item.title }}</h3>
                                <p class="detail">{{ item.detail }}</p>
                                <div class="price">￥{{ item.price }}</div>
                                <span class="time">{{ item.time }}</span>
                                <span class="name">{{ item.name }}</span>
                            </div>
                        </Card>
                    </dd>
                </dl>
            </div>

            <Page class="page" :current="currentPage" :page-size="25" :total="pages" @on-change="changePage"></Page>
            
        </div>

        <div class="detail_want" >
            <Modal v-model="modal1" width="650">
                <h4 style="font-size: 22px;font-weight: bold;margin-top: 20px;">{{ detailData.title }}</h4>
                <div style="color: #848ea5;font-size: 14px;">发布时间：{{ detailData.time }}</div>                
                <p style="margin-top: 10px;margin-bottom: 10px;word-break:normal;word-wrap: break-word;">{{ detailData.detail }}</p>
                <div style="color: #ff5230;font-size: 20px;margin-bottom: 15px;">￥{{ detailData.price }}</div>
                <div style="font-size: 16px;">
                    <span>联系人：{{ detailData.linkPeople }}</span>
                    <span style="margin-left: 15px;">{{ detailData.linkWay }}</span>
                </div>
            </Modal>
        </div>

    </div>
</template>
<script>
export default {
    data(){
        return {
            info: [],
            pages: 100,
            searchWord: '',
            status: '1',
            typeData: [],
            stype: '1',
            chooseTypeid: 0,
            way: '',
            onff: '1',
            modal1: false,
            detailData: {},
            choose: 0,
            value: 0,
            // shufflingData: [],
            currentPage: 1,
            typeList: [{
                value: 'newWant',
                label: '最新需求'
            },{
                value: 'newGoods',
                label: '最新商品'
            },{
                value: 'popularGoods',
                label: '受欢迎商品'
            },{
                value: 'type',
                label: '按分类查找'
            },{
                value: 'search',
                label: '按关键字查找'
            },{
                value: 'first',
                label: '随便逛逛'
            }]
        }
    },
    created: function(){
        this.allType(0);
        // this.infoData(1);
        // if(this.$route.query.search){
        //     this
        // }
        this.way = this.$route.query.type;
        this.status = this.$route.query.status;
        this.searchWord = this.$route.query.search;
        this.onff = this.status;
        switch(this.way){
            case 'newWant': this.newWantData(1);break;
            case 'newGoods': this.newGoodData(1);break;
            case 'popularGoods': this.PGoodData(1);break;
            case 'first': this.infoData(1);break;
            default: this.search();
        }


    },
    watch:{
        stype: function(){
            this.way = 'type';
            this.choose = 0;
            this.onff = this.stype;
            this.search_type(0,1);
        }
    },
    methods: {
        //选择方式
        selectWay(value){
            this.pages = 1;
            let self = this;
            switch(this.way){
                case 'type': self.search_type(self.chooseTypeid,1);break;
                case 'search': self.search_request(1);break;
                case 'newWant': self.newWantData(1);break;
                case 'newGoods': self.newGoodData(1);break;
                case 'popularGoods': self.PGoodData(1);break;
                default: self.infoData(1); 
            }

        },
        //获取受欢迎商品的函数
        PGoodData(page){
            let self = this;
            this.onff = 1;
            this.$http(process.env.API_HOST+'publish/hotSpotGoods',{
                method: 'get',
                params: {
                    'row': 25,
                    'page': page
                }
            }).then(function(res){
                if(res.data.status == 200){
                    // let data = res.data.data.rows;
                    // let list = []; 
                    // for(let i=0;i<data.length;i++){
                    //     let detail = data[i].detail;
                    //     if(detail.length>45){
                    //         detail = detail.substring(0,45) + '...';
                    //     }
                    //     let d = {
                    //         'title': data[i].title,
                    //         'linkPeople': data[i].linkPeople,
                    //         'detail': detail,
                    //         'id': data[i].id,
                    //         'linkWay': data[i].linkWay,
                    //         'picture': data[i].picture
                    //     }
                    //     list.push(d);
                    //     if(i%2 != 0){
                    //         self.popularGoods.push(list);
                    //         list = [];
                    //     }
                    // }
                    self.info = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;                    
                    data.forEach(e1 => {
                        let time = e1.time.split(' ');
                        let detail = e1.detail;
                        if(detail.length>60){
                            detail = detail.substring(0,60)+'...';
                        }
                        let i = {
                            'address': e1.address,
                            'detail': detail,
                            "picture": e1.picture,
                            'price': e1.price,
                            'time': time[0],
                            'title': e1.title,
                            'id': e1.id,
                            'linkPeople':e1.linkPeople,
                            'viewCount': e1.viewCount
                        };
                        self.info.push(i);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //获取新需求的函数
        newWantData(page){
            let self = this;            
            this.onff = 2;
            this.$http(process.env.API_HOST+'publish/lastestWant',{
                method: 'get',
                params: {
                    'row': 25,
                    'page': 1
                }
            }).then(function(res){
                if(res.data.status == 200){
                    // let data = res.data.data.rows;
                    // let list = [];                     
                    // for(let i=0;i<data.length;i++){
                    //     let d = {
                    //         'title': data[i].title,
                    //         'linkPeople': data[i].linkPeople,
                    //         'id': data[i].id,
                    //         'linkWay': data[i].linkWay,
                    //     }
                    //     list.push(d);
                    //     if((i+1)%4 == 0){
                    //         self.newWant.push(list);
                    //         list = [];
                    //     }
                    // }
                    self.info = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;                    
                    data.forEach(e1 => {
                        let time = e1.time.split(' ');
                        let detail = e1.detail;
                        if(detail.length>60){
                            detail = detail.substring(0,60)+'...';
                        }
                        let i = {
                            'address': e1.address,
                            'detail': detail,
                            "picture": e1.picture,
                            'price': e1.price,
                            'time': time[0],
                            'title': e1.title,
                            'id': e1.id,
                            'linkPeople':e1.linkPeople,
                            'viewCount': e1.viewCount
                        };
                        self.info.push(i);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //获取新商品的函数
        newGoodData(page){
            this.onff = 1;
            let self = this;
            this.$http(process.env.API_HOST+'publish/lastestGoods',{
                method: 'get',
                params: {
                    'row': 25,
                    'page': page
                }
            }).then(function(res){
                if(res.data.status == 200){
                    // let data = res.data.data.rows;
                    // let list = [];                     
                    // for(let i=0;i<data.length;i++){
                    //     let detail =  data[i].detail;
                    //     if(detail.length>45){
                    //         detail = data[i].detail.substring(0,45) + '...';
                    //         console.log(detail);
                    //         console.log('detail');
                    //     }
                    //     let d = {
                    //         'title': data[i].title,
                    //         'linkPeople': data[i].linkPeople,
                    //         'id': data[i].id,
                    //         'detail': detail,
                    //         'linkWay': data[i].linkWay,
                    //         'picture': data[i].picture
                    //     }
                    //     list.push(d);
                    //     if((i+1)%3 == 0){
                    //         self.newGoods.push(list);
                    //         list = [];
                    //     }
                    // }
                    // console.log(self.newGoods);
                    self.info = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;                    
                    data.forEach(e1 => {
                        let time = e1.time.split(' ');
                        let detail = e1.detail;
                        if(detail.length>60){
                            detail = detail.substring(0,60)+'...';
                        }
                        let i = {
                            'address': e1.address,
                            'detail': detail,
                            "picture": e1.picture,
                            'price': e1.price,
                            'time': time[0],
                            'title': e1.title,
                            'id': e1.id,
                            'linkPeople':e1.linkPeople,
                            'viewCount': e1.viewCount
                        };
                        self.info.push(i);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //分类查询的请求函数
        search_type(id,page){
            let self = this;
            this.info = [];
            this.$http(process.env.API_HOST+'publish/list',{
                method: 'get',
                params: {
                    'typeId': id,
                    'status': self.stype,
                    'page': page,
                    'row': 25
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.info = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;
                    data.forEach(e1 => {
                        let time = e1.time.split(' ');
                        let detail = e1.detail;
                        if(detail.length>60){
                            detail = detail.substring(0,60)+'...';
                        }
                        let i = {
                            'address': e1.address,
                            'detail': detail,
                            "picture": e1.picture,
                            'price': e1.price,
                            'time': time[0],
                            'title': e1.title,
                            'id': e1.id,
                            'linkPeople':e1.linkPeople,
                            'viewCount': e1.viewCount
                        };
                        self.info.push(i);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //按分类查询
        search_typerequest(id){
            this.search_type(id,1);
            this.way = 'type';
            this.choose = id;
            this.onff = this.stype;
        },
        //获取随便逛逛的数据
        infoData(page){
            let self = this;
            this.info = [];
            this.way = 'first';  
            this.onff = 1;
            this.$http(process.env.API_HOST + 'publish/first',{
                method: 'get',
                params:{
                    row: 25,
                    page: page
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.info = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;                    
                    data.forEach(e1 => {
                        let time = e1.time.split(' ');
                        let detail = e1.detail;
                        if(detail.length>60){
                            detail = detail.substring(0,60)+'...';
                        }
                        let i = {
                            'address': e1.address,
                            'detail': detail,
                            "picture": e1.picture,
                            'price': e1.price,
                            'time': time[0],
                            'title': e1.title,
                            'id': e1.id,
                            'linkPeople':e1.linkPeople,
                            'viewCount': e1.viewCount
                        };
                        self.info.push(i);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //获取商品详情的函数
        detail_good(id){
            // localStorage.setItem("goodId",id);
            // this.$router.push({ path: '/detailInfo',name: 'detailInfo',query: {'id': id}});
            window.open(window.location.origin+'/secondHandMarket-web/#/detailInfo?id='+id);
        },
        //获取需求详情的函数
        detail_want(id){       
            this.modal1 = true;
            let self = this;
            this.$http(process.env.API_HOST+'publish/want/get/'+id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data;
                    let time = data.time.split(' ');
                    self.detailData = {
                        'address': data.address,
                        'detail': data.detail,
                        'id': data.id,
                        'linkPeople': data.linkPeople,
                        'linkWay': data.linkWay,
                        'price': data.price,
                        'time': time[0],
                        'title': data.title,
                    }
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //获取所有分类的函数
        allType(id){
            let self = this;
            this.$http(process.env.API_HOST + 'manager/type/get/'+id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }else{
                    let data = res.data.data;
                    data.forEach(e1 => {
                        let d = {
                            name: '全部',
                            id: e1.id
                        };
                        if(e1.childList)
                        e1.childList.unshift(d);
                    })
                    self.typeData = data;
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //搜索的函数
        search(){
            if(this.searchWord != ''){
                this.search_request(1);
                this.way = 'search';
                this.onff = this.status;
            }else{
                this.$Message.info('请输入查询条件！');
            }
        },
        //搜索的请求函数
        search_request(page){
            let self = this;
            this.info = [];
            this.$http(process.env.API_HOST+'publish/get',{
                method: 'get',
                params: {
                    title: self.searchWord,
                    status: self.status,
                    page: page
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.info = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;                    
                    data.forEach(e1 => {
                        let time = e1.time.split(' ');
                        let detail = e1.detail;
                        if(detail.length>60){
                            detail = detail.substring(0,60)+'...';
                        }
                        let i = {
                            'address': e1.address,
                            'detail': detail,
                            "picture": e1.picture,
                            'price': e1.price,
                            'time': time[0],
                            'title': e1.title,
                            'id': e1.id,
                            'linkPeople':e1.linkPeople,
                            'viewCount': e1.viewCount
                        };
                        self.info.push(i);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //页码改变的函数
        changePage(page){
            let self = this;
            // this.search_request(page);
            switch(this.way){
                case 'type': self.search_type(self.chooseTypeid,page);break;
                case 'search': self.search_request(page);break;
                case 'newWant': this.newWantData(page);break;
                case 'newGoods': this.newGoodData(page);break;
                case 'popularGoods': this.PGoodData(page);break;
                default: self.infoData(page); 
            }
        },
        // collection(id){
        //     let self = this;
        //     this.$http(process.env.API_HOST+'collector/'+localStorage.id+'/add/'+id,{
        //         method: 'get'
        //     }).then(function(res){
        //     }).catch(function(err){
        //         console.log(err);
        //     })
        // }
    }
}
</script>
<style lang="less">
.ivu-carousel-list{
    width: 100%;
}
</style>

<style lang="less" scoped>
    .browseInfo{
        .search{
            min-width: 1400px;
            margin: 0 auto;
            input{
                position: relative;
                top: -158px;
                left: 35%;
                width: 30%;
                height: 46px;
                border: 2px solid #c6c1c1;
                border-radius: 5px;
                outline: none;
                font-size: 20px;
                color: #495060;
                padding-left: 10px;
            }
            button{
                font-size: 18px;
                outline: none;
                position: relative;
                top: -159px;
                left: 30%;
                width: 4.3%;
                height: 35px;
                background: #f5866e;
                color: #fff;
                border: none;
                border-radius: 3px;
                cursor: pointer;
            }
            .search_radio{
                position: relative;
                top: -115px;
                color: #fff;
                margin-left: 70px;
                label{
                    margin-left: 50px;
                }
            }
        }
        .shuffling{
            .img{
                width: 900px;
                height: 500px;
            }
            .detail{
                display: inline-block;
            }
            .title{
                display: inline-block;
            }
        }
        
        .nav{
            width: 1050px;
            margin: 0 auto;
            margin-bottom: 25px;
            .type_title{
                font-size: 14px;
                font-weight: bold;
                line-height: 35px;
                display: inline-block;
                margin-right: 20px;
            }
            .type_child{
                display: inline-block;
                // margin-right: 10px;
                padding: 5px 10px;
                border-radius: 5px;
                cursor: pointer;
            }
            .type_child:hover{
                color: #f5866e;
            }
            .type_child_choose{
                background: #f5866e;
                color: #fff;
            }
        }
        // .dropdown{
        //     float: left;
        //     margin-right: 20px;
        // }
        .info{
            width: 1050px;
            margin: 0 auto;
            dl{
                overflow: hidden;
                .browseInfo_dd{
                    display: inline-block;
                    margin-right: 17px;
                    margin-bottom: 20px;
                    width: 189px;
                    height: 269px;
                    .ivu-card-bordered{
                        height: 100%;
                    }
                }
                .browseInfo_dd2{
                    float: left;
                }
                .detail{
                    margin-top: 15px;
                    margin-bottom: 10px;  
                    word-wrap: break-word;
                    word-break: normal;
                    height: 130px;
                    overflow: hidden;
                }
                .price{
                    font-size: 20px;
                    font-weight: bold;
                    color: #ff5230;
                    margin-top: 5px;
                }
                .count{
                    display: inline-block;
                    font-size: 13px;
                    padding-top: 2px;
                    height: 25px;
                    width: 80px;
                    line-height: 25px;
                    float: right;
                    cursor: pointer;
                }
                h3{
                    cursor: pointer;
                    word-break: normal;
                    height: 24px;
                    overflow: hidden;
                }
                h3:hover{
                    color: #2255DD;
                    text-decoration: underline;
                }
                .time{
                    color: #848ea5;
                }
                .name{
                    float: right;
                    color: #666d7c;
                    font-size: 12px;
                    max-width: 70px;
                    overflow: hidden;
                }
            }
            .page{
                margin-top: 20px;
            }
            
        }
    }
</style>




