<template>
    <div class="admin">
        
        <div  v-if="loginOnff" class="admin_content">
            <div class="menu">
            <Menu theme="light" :according="true" @on-select="handleSelect">
                <MenuItem name="audit">审核信息</MenuItem>
                <MenuItem name="manageType">管理分类</MenuItem>
                <MenuItem name="report">审核举报</MenuItem>
                <MenuItem name="cancelPunish">解除禁止</MenuItem>
            </Menu>
            </div>

            <div class="view">
                <keep-alive>
                    <router-view></router-view>
                </keep-alive>
            </div>
        </div>

        <div v-else class="admin_login">
            <div style="margin-left: 50px;">
                <label>账&nbsp;&nbsp;&nbsp;&nbsp;号</label>
                <Input style="width: 250px;" type="text" v-model.trim="username" placeholder="请输入用户名"></Input>
            </div>
            <div style="margin-left: 50px;margin-top: 20px;">
                <label>密&nbsp;&nbsp;&nbsp;&nbsp;码</label>
                <Input style="width: 250px;" type="password" v-model.trim="password" placeholder="请输入密码"></Input>
            </div>
            <Button style="margin-left: 180px;margin-top: 40px" type="primary" @click="admin_login">登录</Button>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            password: '',
            username: '',
            loginOnff: true
        }
    },
    created: function(){
        // if(localStorage.loginstate){
        //     this.loginOnff = localStorage.loginstate;
        //     console.log(localStorage.loginstate);
        // }
        this.loginOnff = this.ALState;
        console.log(this.loginOnff+'clogin');
    },
    watch: {
        'ALState': function(){
            console.log(this.loginOnff+'wlogin');
            this.loginOnff = this.ALState;
        }
    },
    props: ['ALState'],
    methods: {
        handleSelect(value){
            this.$router.push({path: value});
        },
        admin_login(){
            if(this.username != '' && this.password != ''){
                let self = this;
                this.$http(process.env.API_HOST+'manager/login',{
                    method: 'post',
                    data:{
                        'username': self.username,
                        'password': self.password
                    }
                }).then(function(res){
                    if(res.data.status == 200){
                        console.log('successful');
                        // self.$store.state.loginState = true;
                        self.nameonff = true;
                        self.$emit('adminLState',true);
                        self.loginOnff = true;
                        localStorage.setItem('loginstate',true);
                        let data = res.data.data.data;
                        self.userData = res.data.data.data;
                        delete self.userData.password;
                        delete self.userData.salt;
                        // self.$store.state.loginState = true;
                        localStorage.setItem('id',data.id);
                        localStorage.setItem('userName',data.username);
                        localStorage.setItem('identity','admin');       
                        self.usersname = localStorage.userName; 
                        self.$router.push({path: '/audit'});
                    }else{
                        self.$Message.error(res.data);
                    }
                    self.username = '';
                    self.password = '';
                }).catch(function(err){
                    self.$Message.error('登录失败！');
                })
            }else{
                this.$Message.info('请将信息填写完整');
            }
        }
    }
}
</script>

<style lang="less" scoped>
.admin{
    .img{
        min-width: 1400px;
    }
    .admin_login{
        width: 500px;
        padding: 50px;
        height: 100%;
        margin: 150px auto 245px auto;
        border: 2px solid #f5866e;
    }
    .admin_content{
        overflow: hidden;
        width: 1100px;
        height: 800px;
        margin: 30px auto 0 auto;
        div{
                display: inline-block;         
                float: left;     
                // border: 1px solid #ddd;                  
            }
            .view{
                margin-left: 15px;
                padding: 30px 40px;
                height: 800px;
                background: #f3f3f3;
                width: 840px;
                float: left;
            }
    }
}
</style>



