<template>
    <div id="app">
        <!-- <Affix :offset-top="0"> -->
            <top v-if="onff" :loginstate="loginstate" :searchType="searchType" :searchWord="searchWord" @lState='lState'></top>
            <adminTop v-else :ALState="ALState"  @adminLState="adminLState"></adminTop> 
        <!-- </Affix> -->
        <router-view @search='search' :ALState="ALState" @adminLState="adminLState"></router-view>
        <bottom></bottom>
    </div>
</template>
<script>
import top from './components/top'
import bottom from "./components/bottom"
import adminTop from './components/adminTop'
export default {
    components: { top,bottom,adminTop },
    data: function(){
        return {
            loginstate: localStorage.loginstate?localStorage.loginstate:false,
            searchWord: '',
            searchType: '1',
            onff: true,
            ALState:localStorage.loginstate?localStorage.loginstate:false
        }
    },
    created: function(){
        let name = this.$route.path;
        if(name == '/audit'||name == '/manageType'||name=='/admin'){
            this.onff = false;
        }
    },
    watch: {
        // '$route'(to,form){
        //     if(to.name == 'audit'||to.name == 'manageType'){
        //         console.log('false');
        //         this.onff = false;
        //     }
        // }
    },
    methods: {
        lState(value){
            this.loginstate = value;
        },
        adminLState(value){
            this.ALState = value;
            console.log('ALState'+this.ALState);
        },
        search(svalue,stype){
            this.searchWord = svalue;
            this.searchType = stype;
        }
    }
}
</script>




