<template>
    <div class="orderHistory">
        <p class="p_head">当前位置--订单管理--订单历史</p>
        <div v-if="onff">
            <Select v-model="tType2" @on-change="handleTabel">
                <Option v-for="item in tableList" :key="item.value" :value="item.value">{{ item.label }}</Option>
            </Select>
            <Table height='615' :columns="order_columns" :data="orderData"></Table>
            <Page :total="pages" :page-size="10" @on-change="pageChange"></Page>
        </div>

        <div v-else>
            <span @click="back" class="back_btn">返回</span>
            <div class="reportGood">
                <label>商品标题</label>
                <span class="message">{{ detailInfo.title }}</span>
            </div>
            <div class="reportGood">
                <label>数量</label>
                <span class="message">{{ detailInfo.goodsNum }}</span>
            </div>
            <div class="reportGood">
                <label>总价</label>
                <span class="message">{{ detailInfo.goodsTotal }}</span>
            </div>
            <div class="reportGood">
                <label>日期</label>
                <span class="message">{{ detailInfo.creatTime }}</span>
            </div>
            <div class="reportGood">
                <label>状态</label>
                <span class="message">{{ detailInfo.status }}</span>
            </div>
            <div class="reportGood">
                <label>联系人</label>
                <span class="message">{{ detailInfo.userName }}</span>
            </div>
            <div class="reportGood">
                <label>联系方式</label>
                <span class="message">{{ detailInfo.linkPhone }}</span>
            </div>
            <div class="reportGood">
                <label>留言</label>
                <span class="message">{{ detailInfo.message }}</span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            tType2: '买家',
            tableList: [{
                value: '卖家',
                label: '卖家'
            },{
                value: '买家',
                label: '买家'
            }],
            pages: 100,
                order_columns: [{
                    title: '商品标题',
                    key: 'title',
                    align: 'center',
                    width: 300
                },{
                    title: '订单状态',
                    key: 'status',
                    align: 'center',
                    width: 150
                },{
                    title: '日期',
                    key: 'time',
                    align: 'center',
                    width: 150
                },{
                    title: '操作',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h,params) => {
                        return h('div',[
                            h('Button',{
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                on: {
                                    click: ()=>{
                                        this.onff = false;
                                        this.detailInfo = params.row;
                                        this.detailInfo.index = params.index;
                                    }
                                }
                            },'查看详情')
                        ]);
                    }
                }],
                orderData: [],
                onff: true,
                detailInfo: {}
        }
    },
    created: function(){
        this.getUser(1);
    },
    watch: {
        '$route'(to,from){
            if(to.name == 'orderHistory'){
                this.getUser(1);
                this.onff = true;
            }
        }
    },
    methods: {
        //获取订单列表
        getUser(page){
            let self = this;
            this.$http(process.env.API_HOST+'orders/listByUserId',{
                method: 'get',
                params: {
                    'page': page,
                    'rows': 10
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.orderData = []
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;
                    data.forEach(e1 => {
                        let time = e1.creatTime.split(' ');
                        let status = '';
                        switch(e1.ordersStatus){
                            case 1: status = '已下单';break;
                            case 2: status = '待交易';break;
                            case 3: status = '交易成功';break;
                            default: status = '交易取消';
                        }
                        // let d = {
                        //     'title': e1.title,
                        //     "id": e1.id,
                        //     'time': time[0],
                        //     'status': status,
                        //     'goodsPrice': e1.goodsPrice,
                        //     'goodsNum': e1.goodsNum,
                        //     'totalPrice': e1.goodsPrice*e1.goodsNum
                        // };
                        let d = e1;
                        d.status = status;
                        d.time = time[0];
                        self.orderData.push(d);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //获取订单列表
        getSeller(page){
            let self =  this;
            this.$http(process.env.API_HOST+'orders/listBySellerId',{
                method: 'get',
                params: {
                    'page': page,
                    'rows': 10
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.orderData = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;
                    data.forEach(e1 => {
                        let time = e1.creatTime.split(' ');
                        let status = '';
                        switch(e1.ordersStatus){
                            case 1: status = '已下单';break;
                            case 2: status = '待交易';break;
                            case 3: status = '交易成功';break;
                            default: status = '交易取消';
                        }
                        // let d = {
                        //     'title': e1.title,
                        //     "id": e1.id,
                        //     'time': time[0],
                        //     'price': e1.goodsPrice,
                        //     'status': status,
                        //     'goodsPrice': e1.goodsPrice,
                        //     'goodsNum': e1.goodsNum,
                        //     'totalPrice': e1.goodsPrice*e1.goodsNum
                        // };
                        let d = e1;
                        d.status = status;
                        d.time = time[0];
                        self.orderData.push(d);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //页码改变
        pageChange(page){
            console.log(page);
            if(this.tType2 == '买家'){
                this.getUser(page);
            }else{
                this.getSeller(page);
            }
        },
        //下拉框选择后处理函数
        handleTabel(value){
            if(value == '买家'){
                this.getUser(1);
            }else{
                this.getSeller(1);
            }
        },
        //返回函数
        back(){
            this.onff = true;
        }
    }
}
</script>
<style lang="less" scoped>
.orderHistory{
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .ivu-table-wrapper{
        height: 616px;
    }
    .ivu-page{
        margin-top: 20px;
    }
    .reportGood{
            overflow: hidden;
            margin-bottom: 15px;
            .message{
                display: inline-block;
                width: 500px;
                word-wrap: break-word;
                word-break: normal;
            }
        label{
            font-weight: bold;
            display: inline-block;
            width: 80px;
            float: left;
        }
        dl{
            display: inline-block;
            dd{
                float: left;
                margin-right: 15px;
            }
        }
    }
    .back_btn{
        color: #f5866e;
        font-size: 16px;
        cursor: pointer;
        position: relative;
        top: -35px;
        left: 650px;
    }
    .back_btn:hover{
        text-decoration: underline;
    }
}
</style>



