import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
export default new Vuex.Store({
    state: {
        loginState: false,
        
    },
    getters: {
        getLogin: function(state){
            if(!state.loginState){
                state.loginState = sessionStorage.getItem('loginState');
            }
            return state.loginState;
        }
    },
    mutations: {
        login(state,data){
            state.loginState = data.loginState;
        }
    }
})


