<template>
    <div class="collection">
        <p class="p_head">当前位置--收藏夹</p>
        <div style="height: 686px">
            <dl>
                <dd class="collection_dd" v-for="item in collection" :key="">
                    <Card>
                        <div>
                            <img :src="item.picture" width="160px" height="150px">
                            <h5 class="title" @click="handleDetail(item.id)">{{ item.title }}123</h5>
                            <div class="price">￥{{ item.price }}</div>
                            <div class="closeBtn" @click="deleteCollection(item.id)">删除收藏</div>
                        </div>
                    </Card>
                </dd>
            </dl>
        </div>
        <Page :total="pages" :page-size="6" @on-change="changePage"></Page>
    </div>
</template>
<script>
export default {
    data(){
        return {
            pages: 100,
            collection: [],
            currentPage: 1
        }
    },
    created: function(){
        this.getCollection(1,6);
    },
    watch: {
        '$route'(to,from){
            if(to.name == 'collection'){
                this.getCollection(1,6);
            }
        }
    },
    methods: {
        //查看详情
        handleDetail(id){
            localStorage.setItem('goodId',id);
            // this.$router.push({path: '/detailInfo',name: 'detailInfo'});
            window.open(window.location.origin+'/secondHandMarket-web/#/detailInfo?id='+id);

        },
        //页码改变时调用的函数
        changePage(page){
            this.currentPage = page;
            this.getCollection(page);
        },
        //删除收藏
        deleteCollection(id){
            let self = this;
            this.$http(process.env.API_HOST + 'collector/delete/' + id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 200){
                    self.getCollection(self.currentPage,6);
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    self.$Message.error('操作有误！');
                    console.log(err);
            })
        },
        //获取收藏信息
        getCollection(page,rows){
            let self = this;
            this.$http(process.env.API_HOST + 'collector/list/'+localStorage.id,{
                method: 'get',
                params: {
                    'page': page,
                    'rows': rows
                }
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data.rows;
                    data.forEach(e1 => {
                        var picture = e1.picture.split(';');
                        e1.picture = picture[0];
                    });
                    self.collection = data;
                    self.pages = res.data.data.total;
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    self.$Message.error('获取信息失败！');
                    console.log(err);
            })
        }
    }
}
</script>

<style lang="less" scoped>
.collection{
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .collection_dd{
        display: inline-block;
        margin: 0 35px 10px 0; 
        .title{
            font-size: 18px;
            margin-top: 5px;
            cursor: pointer;
        }
        .title:hover{
            color: #2255DD;
            text-decoration: underline;
        }
        .price{
            font-size: 18px;
            color: #ff5230;
            margin-top: 10px;
        }
        .closeBtn{
            background: rgba(249, 75, 38, 1);
            height: 40px;
            line-height: 40px;
            text-align: center;
            letter-spacing: 5px;
            color: #fff;
            margin-top: 5px;
            cursor: pointer;
        }
        .closeBtn:hover{
            background: rgba(249, 75, 38, .8);
        }
    }
    
}
</style>




