<template>
    <div class="audit">
        <p class="p_head">当前位置--审核商品</p>
        <div v-if="onff == 0">
            <Table height="665px" :columns="table_columns" :data="table_data"></Table>
            <Page :page-size="13" :total="pages" style="margin-top: 10px;" @on-change="pageChange"></Page>
        </div>

        <div class="detail" v-else-if="onff == 1">
            <span class="back_btn" @click="back">返回</span>
            <Row class="detail_div">
                <div>
                    <Col span="3">
                        <label>标题</label>
                    </Col>
                    <Col span="21">
                        <div class="inline-block">{{ goodData.title }}</div>
                    </Col>
                </div>
            </Row>

            <Row class="detail_div">
                <div>
                    <Col span="3">
                    <label>描述</label>
                    </Col>
                    <Col span="21">
                        <div class="inline-block" style="word-break:normal;word-wrap:break-word;">{{ goodData.detail }}</div>
                    </Col>
                </div>
            </Row>

            <Row class="detail_div">
                <div>
                    <Col span="3">
                        <label>价格</label>
                    </Col>
                    <Col span="9">
                        <div class="inline-block">{{ goodData.price }}</div>
                    </Col>
                </div>
                <div>
                    <Col span="3">
                        <label>联系人</label>
                    </Col>
                    <Col span="9">
                        <div class="inline-block">{{ goodData.linkPeople }}</div>
                    </Col>
                </div>
            </Row>

            <Row class="detail_div">
                <div>
                    <Col span="3">
                        <label>联系电话</label>
                    </Col>
                    <Col span="9">
                        <div>{{ goodData.linkWay }}</div>
                    </Col>
                </div>
                <div>
                    <Col span="3">
                        <label>交易地址</label>
                    </Col>
                    <Col span="9">
                        <div>{{ goodData.address }}</div>
                    </Col>
                </div>
            </Row>

            <Row class="detail_div">
                <div>
                    <Col span="3">
                        <label>物品类别</label>
                    </Col>
                    <Col span="9">
                        <div>{{ goodData.type }}</div>
                    </Col>
                </div>
                <div>
                    <Col span="3">
                        <label>时间</label>
                    </Col>
                    <Col span="9">
                        <div>{{ goodData.time }}</div>
                    </Col>
                </div>
            </Row>
            <Row class="detail_div">
                <div>
                    <Col span="3">
                        <label>图片</label>
                    </Col>
                    <Col span="21">
                        <div class="imgs">
                            <dl>
                                <dd v-for="item in goodData.pic" :key="">
                                    <img :src="item" width="120px" height="120px">
                                </dd>
                            </dl>
                        </div>
                    </Col>
                </div>
            </Row>
            <div class="detail_div">
                <label>审核意见</label>
                <Input type="textarea" v-model.trim="checkResult"></Input>
            </div>
            
            <div class="detail_div">
                <Button type="primary" style="margin-left: 280px;margin-right: 15px;" size="large" @click="auditGood(2)">同意发布</Button>
                <Button type="primary" size="large" @click="auditGood(3)">不同意发布</Button>
            </div>
        </div>


    </div>
</template>
<style lang="less" scoped>
.audit{
    width: 100%;
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .detail{
        // margin-left: 200px;
        // width: 500px;
        margin-top: 30px;
        .back_btn{
            color: #f5866e;
            float: right;
            margin-top: -52px;
        }
        .detail_div{
            margin-bottom: 20px;
            label{
                font-size: 16px;
                font-weight: bold;
                display: inline-block;
                width: 80px;
            }   
            .ivu-input-wrapper{
                width: 450px;
            }
            .imgs{
                dl{
                    display: inline-block;
                    dd{
                        display: inline-block;
                        margin-right: 15px;
                    }
                }
            }
        }
        
    }
}
</style>
<script>
export default {
    data(){
        return{
            table_columns: [{
                title: '标题',
                key: 'title',
                align: 'center',
                width: 395
            },{
                title: '联系人',
                key: 'linkPeople',
                align: 'center',
                width: 140
            },{
                title: '日期',
                key: 'time',
                align: 'center',
                width: 120
            },{
                title: '操作',
                align: 'center',
                width: 100,
                key: 'action',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            on: {
                                click: ()=>{
                                    this.onff = 1;
                                    this.goodDetail(params.index);
                                }
                            }
                        },'查看')
                    ]);
                }
            }],
            table_data: [],
            onff: 0,
            pages: 0,
            checkResult: '',
            goodData: {
                title: '',
                detail: '',
                linkPeople: '',
                linkWay: '',
                address: '',
                time: '',
                type: '',
                pic: []
            }
        }
    },
    created: function(){
        this.getInfo(1);
    },
    watch: {
        '$route'(to,from){
            if(to.name == 'audit'){
                this.onff = 0;
                this.getInfo(1);
                this.checkResult = '';
            }
        }
    },
    methods: {
        //审核商品的函数
        auditGood(checkStatus){
            let self = this;
            if(checkStatus==3 && this.checkResult == ''){
                this.$Message.info('请输入审核意见！');
            }else{
                this.$http(process.env.API_HOST+'manager/goods/check',{
                    method: 'put',
                    data: {
                            'checkStatus': checkStatus,
                            'id': self.goodData.id,
                            'checkResult': self.checkResult
                    }
                }).then(function(res){
                    if(res.data.status == 200){
                        self.$Modal.success({
                            title: '提示',
                            content: '<p>已审核该申请！</p>',
                            onOk: ()=>{
                                self.back();
                                self.getInfo(1);
                            }
                        });
                    }else if(res.data.status == 410){
                        self.$Modal.info({
                            title:  '提示',
                            content: '<p>登录信息失效，请重新登录！'
                        });
                    }
                }).catch(function(err){
                        console.log(err);
                })
            }
            
        },
        //“返回”的函数
        back(){
            this.onff = 0;
            this.checkResult = '';
            this.getInfo(1);
        },
        //页码改变调用的函数
        pageChange(page){
            this.getInfo(page);
        },
        //获取待审核商品详情的函数
        goodDetail(index){
            let self = this;
            this.$http(process.env.API_HOST+'publish/goods/get/'+self.table_data[index].id,{
                method: 'get',
                params: {
                    'status': 0
                }
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data;
                    let time = data.time.split(' ');
                    self.goodData = {
                        'address': data.address,
                        'detail': data.detail,
                        'id': data.id,
                        'linkPeople': data.linkPeople,
                        'linkWay': data.linkWay,
                        'price': data.price,
                        'time': time[0],
                        'title': data.title,
                        'pic': data.pic,
                        'type': data.type
                    };
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //获取待审核商品列表
        getInfo(page){
            let self = this;
            this.$http(process.env.API_HOST+'manager/goods/list',{
                method: 'get',
                params: {
                    'page': page,
                    'row': 13
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.table_data = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;
                    data.forEach(e1=> {
                        let time  = e1.time.split(' ');
                        let d = {
                            'title': e1.title,
                            'time': time[0],
                            'linkPeople': e1.linkPeople,
                            'id': e1.id
                        };
                        self.table_data.push(d);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        }
    }
}
</script>




