import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/home'
// import member from '@/components/member'
import browseInfo from '../components/browseInfo'
import detailInfo from '../components/detailInfo'
// import registered from '../components/registered'
// import SDInfo from '../components/SDInfo'
import personData from '../components/personData'
import person from '../view/person'
import deleteOrder from '../components/deleteOrder'
import orderHistory from '../components/orderHistory'
import publishInfo from '../components/publishInfo'
import modifyPublish from '../components/modifyPublish'
import deletePublish from '../components/deletePublish'
// import publishHistory from '../components/publishHistory'
import confirmOrder from '../components/confirmOrder'
import collection from '../components/collection'
import admin from '../view/admin'
import audit from '../components/audit'
import manageType from '../components/manageType'
import confirmUserOrder from '../components/confirmUserOrder'
import report from '../components/report'
import newReport from '../components/newReport'
import cancelReport from '../components/cancelReport'
import reportHistory from '../components/reportHistory'
import cancelPunish from '../components/cancelPunish'
Vue.use(Router)

export default new Router({
  routes: [{
    path: '/browseInfo',
    name: 'browseInfo',
    component: browseInfo
  },{
      path: '/',
      name: 'home',
      component: home
    },{
    //   path: '/member',
    //   name: 'member',
    //   component: member
    // },{
      path: '/detailInfo',
      name: 'detailInfo',
      component: detailInfo
    },{
    //   path: '/registered',
    //   name: 'registered',
    //   component: registered
    // },{
    //   path: './SDInfo',
    //   name: 'SDInfo',
    //   component: SDInfo
    // },{
      path: '/person',
      name: 'person',
      component: person,
      children: [{
        path: '/personData',
        name: 'personData',
        component: personData
      },{
        path: '/deleteOrder',
        name: 'deleteOrder',
        component: deleteOrder
      },{
        path: '/orderHistory',
        name: 'orderHistory',
        component: orderHistory
      },{
        path: '/publishInfo',
        name: 'publishInfo',
        component: publishInfo
      },{
        path: '/modifyPublish',
        name: 'modifyPublish',
        component: modifyPublish
      },{
        path: '/deletePublish',
        name: 'deletePublish',
        component: deletePublish
      },{
      //   path: '/publishHistory',
      //   name: 'publishHistory',
      //   component: publishHistory
      // },{
        path: '/confirmOrder',
        name: 'confirmOrder',
        component: confirmOrder
      },{
        path: '/collection',
        name: 'collection',
        component: collection
      },{
        path: '/confirmUserOrder',
        name: 'confirmUserOrder',
        component: confirmUserOrder
      },{
        path: '/newReport',
        name: 'newReport',
        component: newReport
      },{
        path: '/cancelReport',
        name: 'cancelReport',
        component: cancelReport
      },{
        path: '/reportHistory',
        name: 'reportHistory',
        component: reportHistory
      }]
    },{
      path: '/admin',
      name: 'admin',
      component: admin,
      children: [{
        path: '/audit',
        name: 'audit',
        component: audit
      },{
        path: '/manageType',
        name: 'manageType',
        component: manageType
      },{
        path: '/report',
        name: 'report',
        component: report
      },{
        path: '/cancelPunish',
        name: 'cancelPunish',
        component: cancelPunish
      }]
    }
  ]
})



