<template>
    <div class="confirmUserOrder">
        <p class="p_head">当前位置--订单管理--订单管理(买家)</p>
        <div v-if="onff" style="width: 860px">
            <Table height='615' style="margin-right: 100px" :columns="order_columns" :data="orderData"></Table>
            <Page :total="pages" :page-size="10" @on-change="pageChange"></Page>
        </div>

        <div v-else>
            <span @click="back" class="back_btn">返回</span>
            <div class="reportGood">
                <label>商品标题</label>
                <span class="message">{{ detailInfo.title }}</span>
            </div>
            <div class="reportGood">
                <label>数量</label>
                <span class="message">{{ detailInfo.goodsNum }}</span>
            </div>
            <div class="reportGood">
                <label>总价</label>
                <span class="message">{{ detailInfo.goodsTotal }}</span>
            </div>
            <div class="reportGood">
                <label>日期</label>
                <span class="message">{{ detailInfo.creatTime }}</span>
            </div>
            <div class="reportGood">
                <label>联系人</label>
                <span class="message">{{ detailInfo.userName }}</span>
            </div>
            <div class="reportGood">
                <label>联系方式</label>
                <span class="message">{{ detailInfo.linkPhone }}</span>
            </div>
            <div class="reportGood">
                <label>留言</label>
                <span class="message">{{ detailInfo.message }}</span>
            </div>
            <div style="margin-left: 80px">
                <Button type="primary" @click="confirm">确认收货</Button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            pages: 0,
            order_columns: [{
                title: '商品标题',
                key: 'title',
                align: 'center',
                width: 255
            },{
                title: '单价',
                key: 'goodsPrice',
                align: 'center',
                width: 70
            },{
                title: '数量',
                key: 'goodsNum',
                align: 'center',
                width: 70
            },{
                title: '总价',
                key: 'totalPrice',
                align: 'center',
                width: 70
            },{
                title: '日期',
                key: 'creatTime',
                align: 'center',
                width: 120
            },{
                title: '操作',
                key: 'action',
                width: 170,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            on: {
                                click: ()=>{
                                    this.onff = false;
                                    this.detailInfo = params.row;
                                    this.detailInfo.index = params.index;
                                    this.detailInfo.way = 'sell';
                                }
                            }
                        },'查看详情')
                    ]);
                }
            }],
            // orderData1: [],
            orderData: [],
            onff: true,
            detailInfo: {}
        }
    },
    created: function(){
        let self =  this;
        this.orderList_sell(1);
    },
    watch: {
        '$route'(to,from){
            if(to.name == 'confirmUserOrder'){
                this.orderList_sell(1);
                this.onff = true;
            }
        }
    },
    methods: {
        //确认收货
        confirm(){
            let index = this.detailInfo.index;
            let type = this.detailInfo.way;
            let id = this.orderData[index].id;
            let self = this;
            this.$http(process.env.API_HOST+'orders/receive/'+id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 200){
                    self.$Modal.success({
                            title: '提示',
                            content: '<p>确认成功！</p>',
                            onOk: ()=>{
                                self.back();
                                self.orderData.splice(index,1);
                            }
                        });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }else{
                    self.$Message.info(res.data.msg);
                }
            }).catch(function(err){
                console.log(err);
            });
        },
        //订单列表
        orderList_sell(page){
            let self = this;
            this.$http(process.env.API_HOST+'orders/waitTransaction',{
                method: 'get',
                params: {
                    'page': page,
                    'rows': 10
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.orderData = [];
                    let data = res.data.data.rows;  
                    self.pages = res.data.data.total;
                    data.forEach(e1 => {
                            let time = e1.creatTime.split(' ');
                            // let d = {
                            //     'title': e1.title,
                            //     'creatTime': time[0],
                            //     'id': e1.id,
                            //     'userName': e1.userName,
                            //     'goodsNum': e1.goodsNum,
                            //     'goodsPrice': e1.goodsPrice,
                            //     'totalPrice': e1.goodsNum*e1.goodsPrice
                            // };
                            let d = e1;
                            e1.creatTime = time[0];
                            self.orderData.push(d);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //页码改变
        pageChange(page){
            this.orderList_sell(page);
        },
        //‘返回’函数
        back(){
            this.onff = true;
        }
    }
}
</script>
<style lang="less" scoped>
.confirmUserOrder
{
    width: 100%;
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .ivu-table-wrapper{
        height: 616px;
    }
    .ivu-page{
        margin-top: 20px;
    }
    .reportGood{
            overflow: hidden;
            margin-bottom: 15px;
            .message{
                display: inline-block;
                width: 500px;
                word-wrap: break-word;
                word-break: normal;
            }
        label{
            font-weight: bold;
            display: inline-block;
            width: 80px;
            float: left;
        }
        dl{
            display: inline-block;
            dd{
                float: left;
                margin-right: 15px;
            }
        }
    }
    .back_btn{
        color: #f5866e;
        font-size: 16px;
        cursor: pointer;
        position: relative;
        top: -35px;
        left: 650px;
    }
    .back_btn:hover{
        text-decoration: underline;
    }
}
</style>




