<template>
    <div class="cancelReport">
        <p class="p_head">当前位置--取消举报</p>
        <div v-if="onff" style="width: 860px">
            <Table height='615' style="margin-right: 100px" :columns="reportList_columns" :data="reportList"></Table>
            <Page :total="pages" :page-size="10" @on-change="pageChange"></Page>
        </div>
        
        <div v-else>
            <span @click="back" class="back_btn">返回</span>
            <div class="reportGood">
                <label>商品标题</label>
                <span>{{ detailInfo.orderDto.title }}</span>
            </div>
            <div class="reportGood">
                <label>数量</label>
                <span>{{ detailInfo.orderDto.goodsNum }}</span>
            </div>
            <div class="reportGood">
                <label>总价</label>
                <span>{{ detailInfo.orderDto.goodsTotal }}</span>
            </div>
            <div class="reportGood">
                <label>举报原因</label>
                <span>{{ detailInfo.reason }}</span>
            </div>
            <div class="reportGood">
                <label>举报日期</label>
                <span>{{ detailInfo.reportTime }}</span>
            </div>
            <div class="reportGood">
                <label>图片</label>
                <dl>
                    <dd v-for="item in detailInfo.picArray" :key="">
                        <img :src="item" width="150px" height="150px">
                    </dd>
                </dl>
            </div>
            <div style="margin-left: 80px">
                <Button type="primary" @click="cancelReport">取消举报</Button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            reportList_columns: [{
                title: '标题',
                key: 'title',
                width: 450
            },{
                title: '日期',
                key: 'reportTime',
                width: 160
            },{
                title: '操作',
                key: 'action',
                width: 150,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            on: {
                                click: ()=>{
                                    // this.delpunish(params.row,params.index);
                                    this.onff = false;
                                    this.detailInfo = params.row;
                                }
                            }
                        },'查看详情')
                    ]);
                }
            }],
            pages: 0,
            reportList: [],
            onff: true,
            detailInfo: {}
        }
    },
    created: function(){
        this.getReportList(1);
    },
    watch: {
        '$route'(to,form){
            if(to.name == 'reportHistory'){
                this.getReportList(1);
                this.onff = true;              
            }
        }
    },
    methods: {
        //取消举报
        cancelReport(){
            let self = this;
            this.$http(process.env.API_HOST+'report/delete/'+self.detailInfo.id,{
                method: 'delete',
            }).then(function(res){
                if(res.data.status == 200){
                    self.getReportList(1);
                    self.onff = true;
                    self.$Message.info('取消举报成功！');
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //获取举报列表
        getReportList(page){
            let self = this;
            this.$http(process.env.API_HOST+'report/getWaitCheck',{
                method: 'get',
                params: {
                    'page': page,
                    'rows': 10
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.pages = res.data.data.total;
                    let data =  res.data.data.rows;
                    data.forEach(e1 => {
                        let time = e1.reportTime.split(' ');
                        e1.reportTime = time[0];
                        e1.title = e1.orderDto.title;
                    });
                    self.reportList = data;
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //页码改变
        pageChange(page){
            this.getUserList(page);
        },
        //‘返回’函数
        back(){
            this.onff = true;
        }
    }
}
</script>
<style lang="less" scoped>
.cancelReport{
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    .reportGood{
            overflow: hidden;
            margin-bottom: 15px;
        label{
            font-weight: bold;
            display: inline-block;
            width: 80px;
            float: left;
        }
        dl{
            display: inline-block;
            dd{
                float: left;
                margin-right: 15px;
            }
        }
    }
    .back_btn{
        color: #f5866e;
        font-size: 16px;
        cursor: pointer;
        position: relative;
        top: -35px;
        left: 650px;
    }
    .back_btn:hover{
        text-decoration: underline;
    }
}
</style>



