<template>
    <div class="personData">
        <p class="p_head">当前位置--个人信息--修改信息</p>
        <!-- <img src="../assets/logo.png" width="150px" height="150px"> -->
        <div class="form">
            <Form :model="form" :label-width="100">
                <FormItem label="头像">
                    <div>
                        <Upload ref="upload" :show-upload-list="false" action="#" :before-upload="beforeUpload">
                            <div>
                                <img :src="form.headpic?form.headpic.url:'#'" width="150px" height="150px">
                                <!-- <div class="upload" @click="picUpload">上传</div> -->
                            </div>
                            <!-- <div v-if="form.headpic != ''" class="camera" style="width: 150px;height: 150px;">
                                <!<Icon type="camera" style="position:absolute;top:107px;left: 119px;" size="20"></Icon>  
                            </div> -->
                        </Upload>
                    </div>
                </FormItem>
                <!-- <FormItem label="用户名">
                    <Input v-model.trim="form.username"></Input>
                </FormItem> -->
                <FormItem label="联系方式">
                    <Input v-model.trim="form.mobile"></Input>
                </FormItem>
                <FormItem label="邮箱">
                    <Input v-model.trim="form.email"></Input>
                </FormItem>
                <FormItem label="身份证号">
                    <Input v-model.trim="form.identity"></Input>
                </FormItem>
                <FormItem>
                    <Button class="btn" size="large" @click="modifyInfo" type="primary">修 改 信 息</Button>
                </FormItem>
            </Form>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            form: {
                headpic: {
                    url: '#'
                },
                username: '',
                mobile: '',
                email: '',
                identity: '',
                id: 0
            },
            upload: false
        }
    },
    created: function(){
        this.getPersonData();
        this.form = {};
    },
    methods: {
        // picUpload(){
        //     let self = this;
        //     this.$http(process.env.API_HOST+'user/POST/updImage/'+localStorage.id,{
        //         method: 'post',
        //         headers: {
        //             'Content-Type': 'application/x-www-form-urlencoded'
        //         }
        //     }).then(function(res){
        //         if(res.data.status == 200){
        //             console.log(res.data);
        //         }
        //     }).catch(function(err){
        //         console.log(err);
        //     })
        // },
        //获取个人信息 
        getPersonData(){
            // this.form = this.$router.params;
            let self = this;
            this.$http(process.env.API_HOST+'user/get/'+localStorage.id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data;
                    self.form = {
                        'id': data.id,
                        'identity':data.identity,
                        'mobile': data.mobile,
                        'username': data.username,
                        'email': data.email,
                        'headpic': {
                            'url': data.headpic
                        }
                    };
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //图片上传前调用的函数
        beforeUpload(file){
            let self = this;
            self.form.headpic = [];
            let reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onloadend = function(e){
                file.url = reader.result;
                self.form.headpic = file;
            }
            this.upload = true;
            return false;
        },
        //修改信息
        modifyInfo(){
            // let form = this.form;\
            let form = new FormData();
            let formData = this.form;
            let self = this;
            for(var key in formData){
                if(key != 'headpic')
                form.append(key,formData[key]);
            }
            // form[id] = localStorage.id;
            if(this.upload){
                form.append('uploadFile',self.form.headpic);            
            }else{
                form.append('uploadFile','');
            } 
            this.$http(process.env.API_HOST+'user/modify',{
                method: 'post',
                data: form,
                headers: {
                    "Content-Type": "application/x-www-form-urlencoded"
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.$Message.info('修改成功！');
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        }
    }
}
</script>

<style lang="less" scoped>
.personData{
    overflow: hidden;
    // padding: 20px;
    // margin-left: 30px;
    // margin-top: 50px;
    .p_head{
        color: #f5866e;
        margin-bottom: 10px;
    }
    img{
        float: left;
        
    }
    // .upload{
    //     height: 30px;
    //     width: 150px;
    //     background: #f5866e;
    //     color: #fff;
    // }
    .form{
        width: 380px;
        display: inline-block;
        float: left;
        margin-left: 160px;
        margin-top: 50px;
    }
    .camera{
        background: rgba(0,0,0,0.3);
        i{
            display: initial;
        }
    }
}
</style>



