<template>
    <div class="home">
    <!-- <top></top> -->
        <img src="../assets/03.jpg" style="min-width: 1400px" width="100%">
        <!-- <div class="search">
            <input type="text" v-model.trim="search">
            <button @click="searchBtn">搜 索</button>
            <RadioGroup class="search_radio" v-model="status">
                <Radio label="1"><span>供应</span></Radio>
                <Radio label="2"><span>需求</span></Radio>
            </RadioGroup>
        </div> -->


        <div class="home_content">

            <div class="popularGood">
                <div class="popularGood_content">
                    <h1>受欢迎商品</h1>
                    <Carousel autoplay :autoplay-speed="4000" v-model="PGValue" loop style="width: 1000px;margin-left: 25px;">
                        <CarouselItem v-for="item in popularGoods" :key="item[0].id">
                            <div v-for="item2 in item" :key="item2.id">
                                <div class="pGoods">
                                    <img class="img" width="350px" height="300px" :src="item2.picture">
                                    <h2 class="title" @click="detail_good(item2.id)">{{ item2.title }}</h2>
                                    <div class="detail">{{ item2.detail }}</div>
                                    <span>联系人: {{ item2.linkPeople }}</span>
                                    <span> {{ item2.linkWay }}</span>
                                </div>                
                            </div>
                        </CarouselItem>
                    </Carousel>
                </div>
                <div class="moreBtn" @click="more('popularGoods',1)">查看更多</div>
            </div>

            <div class="newGoods_content">
                <h1>最新商品</h1>
                    <Carousel autoplay  :autoplay-speed="5000" v-model="ngvalue" loop>
                        <CarouselItem v-for="item in newGoods" :key="item[0].id">
                            <div class="nGoods">
                                <div class="newGoods1">
                                    <img class="img" width="350px" height="300px" :src="item[0].picture">
                                    <div>
                                        <h2 class="title1" @click="detail_good(item[0].id)">{{ item[0].title }}</h2>
                                        <div class="detail">{{ item[0].detail }}</div>
                                        <div>联系人: {{ item[0].linkPeople }}</div>
                                        <div>联系方式: {{ item[0].linkWay }}</div>
                                    </div>
                                </div>         
                                <div class="newGoods2">
                                    <img style="float:left" class="img" width="230px" height="150px" :src="item[1].picture">
                                    <div style="float:left;margin-left: 10px;">
                                        <h2 class="title2" @click="detail_good(item[1].id)">{{ item[1].title }}</h2>
                                        <div class="detail">{{ item[1].detail }}</div>
                                        <div>联系人: {{ item[1].linkPeople }}</div>
                                        <div>联系方式: {{ item[1].linkWay }}</div>
                                    </div>
                                </div>         
                                <div class="newGoods2">
                                    <img class="img" style="float:left" width="230px" height="150px" :src="item[2].picture">
                                    <div style="float:left;margin-left: 10px;">
                                        <h2 class="title2" @click="detail_good(item[2].id)">{{ item[2].title }}</h2>
                                        <div class="detail">{{ item[2].detail }}</div>
                                        <div>联系人: {{ item[2].linkPeople }}</div>
                                        <div>联系方式: {{ item[2].linkWay }}</div>
                                    </div>
                                </div>  
                            </div>
                        </CarouselItem>
                    </Carousel>
                    <div class="moreBtn" @click="more('newGoods',1)">查看更多</div>
            </div>

            <div class="newWant_content">
                <div class="nWant_content">
                    <h1>最新需求</h1>
                    <Carousel autoplay  :autoplay-speed='5000' v-model="NWvalue" loop>
                        <CarouselItem v-for="item in newWant" :key="item[0].id">
                            <div v-for="item2 in item" :key="item2.id" class="newWant" style="">
                                <h2 class="title" @click="detail_want(item2.id)">{{ item2.title }}</h2>
                                <div>联系人: {{ item2.linkPeople }}</div>
                                <div>联系方式: {{ item2.linkWay }}</div>
                            </div>
                        </CarouselItem>
                    </Carousel>
                </div>
                <div class="moreBtn" @click="more('newWant',2)">查看更多</div>
                
            </div>

            <div class="first_content">
                <div>
                    <h1>随 便 逛 逛</h1>
                    <dl>
                        <dd v-for="item in firstData" :key="item.id">
                        <Card>
                            <div class="info_div">
                                <img :src="item.picture" width="155px" height="155px">
                                <h3 @click="detail_good(item.id)">{{ item.title }}</h3>
                                <div>
                                    <span class="price">￥{{ item.price }}</span>
                                    <span class="count">浏览数：{{ item.viewCount }}</span>
                                </div>
                                <span class="time">{{ item.time }}</span>
                                <span class="name">{{ item.linkPeople }}</span>
                            </div>
                        </Card>
                        </dd>
                    </dl>
                    <div class="moreBtn" @click="more('first',1)">显示更多</div>
                </div>
            </div>

            <div class="detail_want" >
                <Modal v-model="modal1" width="650">
                    <h4 style="font-size: 22px;font-weight: bold;margin-top: 20px;">{{ detailData.title }}</h4>
                    <div style="color: #848ea5;font-size: 14px;">发布时间：{{ detailData.time }}</div>                
                    <p style="margin-top: 10px;margin-bottom: 10px;word-break:normal;word-wrap: break-word;">{{ detailData.detail }}</p>
                    <div style="color: #ff5230;font-size: 20px;margin-bottom: 15px;">￥{{ detailData.price }}</div>
                    <div style="font-size: 16px;">
                        <span>联系人：{{ detailData.linkPeople }}</span>
                        <span style="margin-left: 15px;">{{ detailData.linkWay }}</span>
                    </div>
                </Modal>
            </div>

        </div>
        
    </div>
</template>
<script>
// import top from './top'
export default {
    // components: { top },
    data(){
        return {
            search: '',
            status: '1',
            popularGoods: [],
            newWant: [],
            newGoods: [],
            PGValue: 1,
            NWvalue: 1,
            ngvalue: 1,
            modal1: false,
            detailData: {},
            firstData: []
        }
    },
    created: function(){
        this.PGoodData();
        this.newWantData();
        this.newGoodData();
        this.getFirst(1);
    },
    methods: {
        //获取随便逛逛的数据
        getFirst(page){
            let self = this;
            this.$http(process.env.API_HOST + 'publish/first',{
                method: 'get',
                params:{
                    row: 20,
                    page: page
                }
            }).then(function(res){
                if(res.data.status == 200){
                    self.firstData = [];
                    let data = res.data.data.rows;
                    self.pages = res.data.data.total;                    
                    data.forEach(e1 => {
                        let time = e1.time.split(' ');
                        let detail = e1.detail;
                        if(detail.length>60){
                            detail = detail.substring(0,60)+'...';
                        }
                        let i = {
                            'address': e1.address,
                            'detail': detail,
                            "picture": e1.picture,
                            'price': e1.price,
                            'time': time[0],
                            'title': e1.title,
                            'id': e1.id,
                            'linkPeople':e1.linkPeople,
                            'viewCount': e1.viewCount
                        };
                        self.firstData.push(i);
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //获取商品详情的函数
        detail_good(id){
            console.log('detail');
            // localStorage.setItem("goodId",id);
            // this.$router.push({ path: '/detailInfo',name: 'detailInfo',query: {'id': id}});
            window.open(window.location.origin+'/secondHandMarket-web/#/detailInfo?id='+id);
        },
        //获取需求详情的函数
        detail_want(id){    
            console.log('detail');               
            this.modal1 = true;
            let self = this;
            this.$http(process.env.API_HOST+'publish/want/get/'+id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data;
                    let time = data.time.split(' ');
                    self.detailData = {
                        'address': data.address,
                        'detail': data.detail,
                        'id': data.id,
                        'linkPeople': data.linkPeople,
                        'linkWay': data.linkWay,
                        'price': data.price,
                        'time': time[0],
                        'title': data.title,
                    }
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //"查看更多"
        more(value,status){
            window.open(window.location.origin+'/secondHandMarket-web/#/browseInfo?type='+value+'&status='+status+'&search=');
        },
        //搜索框的函数
        searchBtn(){
            if(this.search == ''){
                this.$Message.info('请输入查询条件！');
            }else{
                // this.$router.push({path: '/browseInfo',query: {search: this.search,status: this.status}});
                window.open(window.location.origin+'/secondHandMarket-web/#/browseInfo?type=search&status='+this.status+'&search='+this.search);                
                this.$emit('search',this.search,this.status);
                // window.open(window.location.origin+'/secondHandMarket-web/#/detailInfo?status='+this.status+'&search='+this.search);
            }
        },
        //获取受欢迎商品的函数
        PGoodData(){
            let self = this;
            this.$http(process.env.API_HOST+'publish/hotSpotGoods',{
                method: 'get',
                params: {
                    'row': 10,
                    'page': 1
                }
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data.rows;
                    let list = []; 
                    for(let i=0;i<data.length;i++){
                        let detail = data[i].detail;
                        if(detail.length>45){
                            detail = detail.substring(0,45) + '...';
                        }
                        let d = {
                            'title': data[i].title,
                            'linkPeople': data[i].linkPeople,
                            'detail': detail,
                            'id': data[i].id,
                            'linkWay': data[i].linkWay,
                            'picture': data[i].picture
                        }
                        list.push(d);
                        if(i%2 != 0){
                            self.popularGoods.push(list);
                            list = [];
                        }
                    }
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //获取新需求的函数
        newWantData(){
            let self = this;
            this.$http(process.env.API_HOST+'publish/lastestWant',{
                method: 'get',
                params: {
                    'row': 20,
                    'page': 1
                }
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data.rows;
                    let list = [];                     
                    for(let i=0;i<data.length;i++){
                        let d = {
                            'title': data[i].title,
                            'linkPeople': data[i].linkPeople,
                            'id': data[i].id,
                            'linkWay': data[i].linkWay,
                        }
                        list.push(d);
                        if((i+1)%4 == 0){
                            self.newWant.push(list);
                            list = [];
                        }
                    }
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        },
        //获取新商品的函数
        newGoodData(){
            let self = this;
            this.$http(process.env.API_HOST+'publish/lastestGoods',{
                method: 'get',
                params: {
                    'row': 15,
                    'page': 1
                }
            }).then(function(res){
                if(res.data.status == 200){
                    let data = res.data.data.rows;
                    let list = [];                     
                    for(let i=0;i<data.length;i++){
                        let detail =  data[i].detail;
                        if(detail.length>45){
                            detail = data[i].detail.substring(0,45) + '...';
                        }
                        let d = {
                            'title': data[i].title,
                            'linkPeople': data[i].linkPeople,
                            'id': data[i].id,
                            'detail': detail,
                            'linkWay': data[i].linkWay,
                            'picture': data[i].picture
                        }
                        list.push(d);
                        if((i+1)%3 == 0){
                            self.newGoods.push(list);
                            list = [];
                        }
                    }
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                console.log(err);
            })
        }
    }
}
</script>
<style lang="less" scoped>
    .home{
        // img{
        //     min-width: 1400px;
        // }
        .search{
            min-width: 1400px;
            margin: 0 auto;
            .search_radio{
                position: relative;
                top: -115px;
                color: #fff;
                margin-left: 70px;
                label{
                    margin-left: 50px;
                }
            }
            input{
                position: relative;
                top: -158px;
                left: 35%;
                width: 30%;
                height: 46px;
                border: 2px solid #c6c1c1;
                border-radius: 5px;
                outline: none;
                font-size: 20px;
            }
            button{
                font-size: 18px;
                outline: none;
                position: relative;
                top: -159px;
                left: 30%;
                width: 4.3%;
                height: 35px;
                // background: #f5866e;
                background: #f5b46e;
                color: #fff;
                border: none;
                border-radius: 3px;
                cursor: pointer;
            }
        }
        .first_content{
            margin: 0 auto;
            .moreBtn{
                background: #fff;
                color: #f5866e;
                font-size: 25px;
                border: 2px solid #f5866e;
                width: 200px;
                height: 80px;
                line-height: 80px;    
                text-align: center;   
                margin: 0 auto;    
                cursor: pointer;     
            }
            .moreBtn:hover{
                background: #f5866e;
                color: #fff;
            }
            h1{
                color: #fff;
                background: #f5866e;
                padding: 15px 0;
                text-align: center;
                border: 1px solid #f5866e;
            }
            dl{
                width: 920px;
                margin: 0 auto;
                dd{
                    display: inline-block;
                    margin: 20px;
                }
            }
            .browseInfo_dd{
                display: inline-block;
                margin-right: 17px;
                margin-bottom: 20px;
                width: 189px;
                height: 269px;
                .ivu-card-bordered{
                    height: 100%;
                }
            }
            .detail{
                    margin-top: 15px;
                    margin-bottom: 10px;  
                    word-wrap: break-word;
                    word-break: normal;
                    height: 130px;
                    overflow: hidden;
                }
                .price{
                    font-size: 20px;
                    font-weight: bold;
                    color: #ff5230;
                    margin-top: 5px;
                }
                .count{
                    display: inline-block;
                    font-size: 13px;
                    padding-top: 2px;
                    height: 25px;
                    width: 80px;
                    line-height: 25px;
                    float: right;
                    cursor: pointer;
                }
                h3{
                    cursor: pointer;
                    word-break: normal;
                    height: 24px;
                    overflow: hidden;
                }
                h3:hover{
                    color: #2255DD;
                    text-decoration: underline;
                }
                .time{
                    color: #848ea5;
                }
                .name{
                    float: right;
                    color: #666d7c;
                    font-size: 12px;
                    max-width: 70px;
                    overflow: hidden;
                }
        }
        .home_content{
                // width: 1000px;        
                // margin: 0 auto;    
            .popularGood{
                // background: #f5b46e;
                padding: 50px 0;
                margin-bottom: 120px;
                .popularGood_content{
                    width: 1100px;
                    margin: 0 auto;
                    padding: 20px 30px; 
                    // background: #fac893;
                    h1{
                        // color: #fff;
                        margin-left: 450px;
                        color: #f5866e;
                        margin-bottom: 40px;
                    }
                }
                .moreBtn{
                    width: 280px;
                    // background: #fac893;
                    height: 65px;
                    font-size: 25px;
                    line-height: 65px;
                    text-align: center;
                    font-weight: bold;
                    color: #fff;
                    margin: 30px auto;
                    cursor: pointer;
                    background: #f5866e;                    
                    // border: 2px solid #fff;
                }
                .moreBtn:hover{
                    background: #fff;
                    border: 2px solid #f5866e;
                    color: #f5866e;
                }

            }
            .newGoods_content{
                width: 1100px;
                margin: 20px auto 80px auto;
                h1{
                    color: #f5b46e;
                    text-align: center;
                    margin-bottom: 45px;
                }
                .moreBtn{
                    width: 100%;
                    height: 65px;
                    background: #f5b46e;
                    font-size: 25px;
                    color: #fff;
                    line-height: 65px;
                    text-align: center;
                    margin-top: 50px;
                    cursor: pointer;
                }
                .moreBtn:hover{
                    background: #fff;
                    color: #f5b46e;
                    border: 2px solid #f5b46e;
                }
                .nGoods{
                    .newGoods1{
                        border: 3px solid #f5b46e;
                        padding: 15px;
                        border-radius: 5px;
                        display: inline-block;
                        float: left;
                        width: 390px;
                        margin-left: 15px;
                        height: 450px;
                        .title1{
                            margin-bottom: 15px;
                            cursor: pointer;
                        }
                        .title1:hover{
                            color: #57a3f3;
                            text-decoration: underline;
                        }
                        .detail{
                            word-break: normal;
                            word-wrap: break-word;
                        }
                    }
                    .newGoods2{
                        border: 3px solid #f5b46e;
                        padding: 25px;
                        border-radius: 5px;
                        display: inline-block;
                        float: left;
                        width: 610px;
                        overflow: hidden;
                        margin-left: 50px;
                        height: 210px;
                        .title2{
                            margin-bottom: 10px;
                            cursor: pointer;
                        }
                        .title2:hover{
                            color: #57a3f3;
                            text-decoration: underline;
                        }
                        .detail{
                            word-break: normal;
                            word-wrap: break-word;
                            width: 310px;
                        }
                    }
                    .newGoods2:last-child{
                        margin-top: 30px;
                    }
                }
            }
            .newWant_content{
                // width: 900px;
                width: 100%;
                margin: 120px auto 120px auto;
                background: #f5b46e;
                padding: 50px;
                .moreBtn{
                    width: 250px;
                    height: 65px;
                    margin: 0 auto;
                    border: 2px solid #495060;
                    background: #fac893;
                    font-size: 25px;                    
                    // background: #fac893;
                    color: #495060;
                    // color: #fff;
                    line-height: 65px;
                    text-align: center;
                    margin-top: 50px;
                    cursor: pointer;
                }
                .moreBtn:hover{
                    background: #f5b46e;
                }
                .nWant_content{
                    width: 900px;
                    background: #fac893;
                    margin: 0 auto;
                    padding: 20px 0;
                    h1{
                        // color: #f5866e;
                        color: #495060;
                        text-align: center;
                        margin-bottom: 45px;

                    }
                    .newWant{
                        border: 3px solid #495060;
                        padding: 30px;
                        width: 380px;
                        height: 150px;
                        border-radius: 5px;
                        display: inline-block;
                        float: left;
                        overflow: hidden;
                        margin-left: 50px;
                        margin-bottom: 15px;
                        .title{
                            cursor: pointer;
                            margin-bottom: 10px;
                        }
                        .title:hover{
                            color: #57a3f3;
                            text-decoration: underline;
                        }
                    }
                }
            }
            .pGoods{
                width: 410px;
                height: 450px;
                padding: 30px;
                border: 3px solid #f5866e;
                // border: 1px solid #fff;
                border-radius: 5px;
                float: left;
                .title{
                    cursor: pointer;
                }
                .title:hover{
                    color: #57a3f3;
                    text-decoration: underline;
                }
                .detail{
                    word-break: normal;
                    word-wrap: break-word;
                }
            }
            .pGoods:last-child{
                margin-left: 60px;
            }
        }
        
    }
</style>





