<template>
    <div class="manageType">
        <div v-if="onff">
            <Table :columns="type_columns" :data="type_data"></Table>
        </div>
        <div v-else>
            <span class="back_btn" @click="back">返回</span>
            <Tabs value="tab1" style="width: 750px;height: 700px;">
                <TabPane label="修改名称" name="tab1">
                    <div class="change" style="margin-top: 100px;">
                        <!-- <Select v-model="model1">
                            <Option v-for="item in type_data" :value="item" :key="item.id">{{ item.name }}</Option>
                        </Select> -->
                        <label>原名称：</label>
                        <Select v-model="model1" style="width: 300px">
                            <Option v-for="item in child.childList" :value="item.id" :key="item.id">{{ item.name }}</Option>
                        </Select>
                    </div>
                    <div class="change">
                        <label>新名称：</label>
                        <Input type="text" v-model="modifyNick" style="width: 300px"></Input>
                    </div>
                    <div class="change_btn">
                        <Button type="primary" size="large" @click="newName">确认修改</Button>
                    </div>
                </TabPane>

                <TabPane label="删除子类" name="tab2">
                    <Table :columns="childType_columns" :data="child.childList"></Table>
                </TabPane>
                <TabPane label="新增分类" name="tab3">
                    <div class="add" style="margin-top: 100px;margin-bottom: 20px;">
                        <label>类型：</label>
                        <Select v-model="model2" style="width: 300px">
                            <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
                        </Select>
                    </div>
                    <div class="add">
                        <label>名称：</label>
                        <Input type="text" style="width: 300px" v-model.trim="newNick"></Input>
                    </div>
                    <Button type="primary" class="add_btn" size="large" @click="addType">确认添加</Button>
                </TabPane>
            </Tabs>
        </div>
        <!-- <div>
            <Row class="expand-row">
                <Col span="12">
                    <span class="expand-key">子分类</span>
                    <span>{{ row }}</span>
                </Col>
            </Row>
        </div> -->
    </div>
</template>
<script>
export default {
    data(){
        return {
            row: {},
            type_columns: [{
                title: '分类名称',
                key: 'name',
                width: 550,
                align: 'center'
            },{
                title: '操作',
                key: 'action',
                width: 200,
                align: 'center',
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'error',
                                size: 'default'
                            },
                            on: {
                                click: () => {
                                    console.log('shanchu');
                                    this.deleteType(params,this.type_data);
                                }
                            }
                        },'删除'),
                        h('Button',{
                            props: {
                                type: 'primary',
                                size: 'default'
                            },
                            on: {
                                click: () => {
                                    this.child = params.row;
                                    this.onff = false;
                                }
                            }
                        },'修改')
                    ])
                }
            }],
            type_data: [],
            onff: true,
            model1: '',
            child: {},
            nameList1: [],
            // nameList2: [],
            modifyNick: '',
            newNick: '',
            childType_columns: [{
                title: '子分类',
                key: 'name',
                align: 'center',
                width: 600
            },{
                title: '操作',
                key: 'action',
                width: 140,
                render: (h,params) => {
                    return h('div',[
                        h('Button',{
                            props: {
                                type: 'error'
                            },
                            on: {
                                click: () => {
                                    this.deleteType(params,this.child.childList);
                                    console.log(params);
                                }
                            }
                        },'删除')
                    ])
                }
            }],
            model2: '',            
            typeList: [{
                label: '主类',
                value: '1'
            },{
                label: '子类',
                value: '2'
            }]
        }
    },
    created: function(){
        this.getType(0);
    },
    watch: {
        '$route'(to,from){
            if(to.name == 'manageType'){
                this.getType(0);
                this.onff = true;
            }
            
        }
    },
    methods: {
        //返回函数
        back(){
            this.onff = true;
            this.newNick = '';
            this.model2 = '';
            this.modifyNick = '';
            this.model1 = '';

        },
        //增加分类
        addType(){
            if(this.newNick != '' && this.model2 != ''){
                let pid = 0;
                if(this.model2 = '2'){
                    pid = this.child.id;
                };
                let self = this;
                this.$http(process.env.API_HOST+'manager/type/add',{
                    method: 'post',
                    data: {
                        'parentId': pid,
                        'name': self.newNick
                    }
                }).then(function(res){
                    if(res.data.status == 200){
                        self.$Message.info('添加成功！');
                        self.newNick = '';
                        self.model2 = '';
                        self.getType(0);
                    }else if(res.data.status == 410){
                        self.$Modal.info({
                            title:  '提示',
                            content: '<p>登录信息失效，请重新登录！'
                        });
                    }else{
                        self.$Message.error('添加失败！'+res.data.data);
                    }
                }).catch(function(err){
                        console.log(err);
                })
            }else{
                this.$Message.error('请将信息填写完整！');
            }
            
        },
        //删除分类
        deleteType(params,data){
            let self = this;
            this.$http(process.env.API_HOST+'manager/type/delete/'+params.row.id,{
                method: 'delete'
            }).then(function(res){
                if(res.data.status == 200){
                    // self.child.childList.splice(params.index,1);
                    data.splice(params.index,1); 
                    self.getType(0);             
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //获取分类
        getType(id){
            let self = this;
            this.$http(process.env.API_HOST+'manager/type/get/'+id,{
                method: 'get'
            }).then(function(res){
                if(res.data.status == 200){
                    self.type_data = [];
                    let data = res.data.data;
                    data.forEach(e1 => {
                        self.type_data.push({'id': e1.id,'name': e1.name,'childList': e1.childList});
                        // self.nameList1.push({'id': e1.id,'name': e1.name});
                    });
                }else if(res.data.status == 410){
                    self.$Modal.info({
                        title:  '提示',
                        content: '<p>登录信息失效，请重新登录！'
                    });
                }
            }).catch(function(err){
                    console.log(err);
            })
        },
        //删除分类
        // removeType(id){
        //     let self = this;
        //     this.$http(process.env.API_HOST+'manager/type/delete/'+id,{
        //         method: 'delete'
        //     }).then(function(res){
        //         if(res.data.status == 200){
        //             self.getType(0);
        //         }else if(res.data.status == 410){
        //             self.$Modal.info({
        //                 title:  '提示',
        //                 content: '<p>登录信息失效，请重新登录！'
        //             });
        //         }
        //     }).catch(function(err){
        //             console.log(err);
        //     })
        // },
        //修改分类名字
        newName(){
            if(this.modifyNick != '' && this.model1 != ''){
                let self = this;
                this.$http(process.env.API_HOST+'manager/type/update',{
                    method: 'put',
                    data: {
                        'id': self.model1,
                        'name': self.modifyNick
                    }
                }).then(function(res){
                    if(res.data.status == 200){
                        self.$Message.info('修改成功！');
                        self.modifyNick = '';
                        self.model1 = '';
                        self.getType(0);
                    }else if(res.data.status == 410){
                        self.$Modal.info({
                            title:  '提示',
                            content: '<p>登录信息失效，请重新登录！'
                        });
                    }else{
                        self.$Message.error('修改失败！'+res.data.data);
                    }
                }).catch(function(err){
                        console.log(err);
                })
            }else{
                this.$Message.error('请将信息填写完整！');
            }
            
        }
    }
}
</script>

<style lang="less" scoped>
.manageType{
    .change{
        margin-left: 180px;
        margin-bottom: 30px;
    }
    .change_btn{
        margin-left: 350px;
        margin-top: 10px;
    }
    .add{
        margin-left: 200px;
    }
    .add_btn{
        margin-left: 350px;
        margin-top: 50px;
    }
    .back_btn{
        color: #f5866e;
        display: inline-block;
        position: relative;
        left: 700px;
        cursor: pointer;
    }
    .back_btn:hover{
        text-decoration: underline;
    }
}
</style>



